export default function (

	{
		webgl_rendering_context,
		Buffer,
	},
)
{
	const gl = webgl_rendering_context;

	class VertexBuffer extends Buffer
	{
		bind ()
		{
			gl.bindBuffer(gl.ARRAY_BUFFER, this.handle);

			return this;
		}

		data (srcData, usage = gl.STATIC_DRAW)
		{
			gl.bufferData(gl.ARRAY_BUFFER, srcData, usage);

			return this;
		}

		subData (offset, srcData)
		{
			gl.bufferSubData(gl.ARRAY_BUFFER, offset, srcData);

			return this;
		}

		attrib (location = 0, size = 3, type = gl.FLOAT, normalized = 0, stride = 0, offset = 0)
		{
			gl.vertexAttribPointer(location, size, type, normalized, stride, offset);

			return this;
		}

		static bind (vertexBuffer)
		{
			gl.bindBuffer(gl.ARRAY_BUFFER, vertexBuffer.handle);

			return VertexBuffer;
		}

		static unbind ()
		{
			gl.bindBuffer(gl.ARRAY_BUFFER, null);

			return VertexBuffer;
		}

		static data (srcData, usage = gl.STATIC_DRAW)
		{
			gl.bufferData(gl.ARRAY_BUFFER, srcData, usage);

			return VertexBuffer;
		}

		static subData (offset, srcData)
		{
			gl.bufferSubData(gl.ARRAY_BUFFER, offset, srcData);

			return VertexBuffer;
		}
	}

	return VertexBuffer;
}
