// webgl object wrappers
import _Buffer from './wrappers/buffer';
import _Renderbuffer from './wrappers/renderbuffer';
import _Framebuffer from './wrappers/framebuffer';
import _VertexArray from './wrappers/vertex-array';
import _VertexAttrib from './wrappers/vertex-attrib';
import _Texture from './wrappers/texture';
import _Shader from './wrappers/shader';
import _Program from './wrappers/program';

// data objects
import _Mat2 from './data-objects/mat-two';
import _Mat3 from './data-objects/mat-three';
import _Mat4 from './data-objects/mat-four';
import _Vec1 from './data-objects/vec-one';
import _Vec2 from './data-objects/vec-two';
import _Vec3 from './data-objects/vec-three';
import _Vec4 from './data-objects/vec-four';
import _Quat from './data-objects/quat';

// derivatives
import _VertexBuffer from './derivatives/buffer/vertex-buffer';
import _IndexBuffer from './derivatives/buffer/index-buffer';
import _Texture2D from './derivatives/texture/texture-two';
import _Camera from './derivatives/camera';
import _Obj from './derivatives/obj';
import _Transition from './derivatives/transition';
import _Time from './derivatives/time';
import _Orbit from './derivatives/orbit';
import _Raycaster from './derivatives/raycaster';
import _Gumball from './derivatives/gumball';
import _UniformSet from './derivatives/uniform-set';



export default class WebGL
{
	constructor (options = {})
	{
		// this.class = 'WebGL';
		this.stateFlag = new Uint8Array([ 1 ]);
		this.$ = () => (this.stateFlag[0] = 1);
		this.extensions = {};
		this.contextAttributes = {};
		this.canvas = options.canvas || document.createElement('canvas');
		this.errorCallback = options.errorCallback || (() => 0);
		this.contextType = options.contextType || 'webgl';
		this.DataArrayConstructor = options.dataArrayConstructor || Float32Array;

		Object.assign(

			this.contextAttributes,

			{
				depth: true,
				stencil: false,
				alpha: false,
				antialias: true,
				premultipliedAlpha: false,
				preserveDrawingBuffer: false,
				failIfMajorPerformanceCaveat: false,
				powerPreference: 'default',
			},

			options.contextAttributes,
		);

		try
		{
			this.webgl_rendering_context =
				this.canvas.getContext(this.contextType, this.contextAttributes) ||
				this.canvas.getContext(`experimental-${ options.contextType }`, this.contextAttributes);
		}
		catch (evt)
		{
			this.errorCallback(evt);
		}

		const gl = this.webgl_rendering_context;

		// https://www.khronos.org/registry/webgl/extensions/
		if (options.extensions)
		{
			options.extensions.forEach((elm) => (this.extensions[elm] = gl.getExtension(elm) || gl.getExtension(`MOZ_${ elm }`) || gl.getExtension(`WEBKIT_${ elm }`)));
		}

		// client-dependent screen and webgl properties
		this.availableExtensions = gl.getSupportedExtensions();
		this.minDpr = options.minDpr || 1;
		this.maxDpr = options.maxDpr || 999;
		this.dpr = options.dpr || Math.min(Math.max(window.devicePixelRatio || 1, this.minDpr), this.maxDpr);
		this.maxTextureUnits = gl.getParameter(gl.MAX_TEXTURE_IMAGE_UNITS);
		this.textureUnits = new Array(this.maxTextureUnits).fill(null).map((elm, i) => gl[`TEXTURE${ i }`]);

		if (this.extensions.EXT_texture_filter_anisotropic)
		{
			this.maxAnisotropy = gl.getParameter(this.extensions.EXT_texture_filter_anisotropic.MAX_TEXTURE_MAX_ANISOTROPY_EXT);
		}
		//

		this.Buffer = _Buffer(this);
		this.Renderbuffer = _Renderbuffer(this);
		this.Framebuffer = _Framebuffer(this);
		this.VertexArray = _VertexArray(this);
		this.Shader = _Shader(this);
		this.Program = _Program(this);
		this.VertexAttrib = _VertexAttrib(this);
		this.Texture = _Texture(this);

		this.Mat2 = _Mat2(this);
		this.Mat3 = _Mat3(this);
		this.Mat4 = _Mat4(this);
		this.Vec1 = _Vec1(this);
		this.Vec2 = _Vec2(this);
		this.Vec3 = _Vec3(this);
		this.Vec4 = _Vec4(this);
		this.Quat = _Quat(this);

		this.VertexBuffer = _VertexBuffer(this);
		this.IndexBuffer = _IndexBuffer(this);
		this.Texture2D = _Texture2D(this);
		this.Camera = _Camera(this);
		this.Obj = _Obj(this);
		this.Transition = _Transition(this);
		this.Time = _Time(this);
		this.Orbit = _Orbit(this);
		this.Raycaster = _Raycaster(this);
		this.Gumball = _Gumball(this);
		this.UniformSet = _UniformSet(this);

		// add clone method to all constructors
	}

	viewport (xOrig, yOrig, width, height)
	{
		this.webgl_rendering_context.viewport(xOrig * this.dpr, yOrig * this.dpr, width * this.dpr, height * this.dpr);

		return this;
	}

	setSize (xOrig, yOrig, width, height)
	{
		this.dpr = Math.min(Math.max(window.devicePixelRatio || 1, this.minDpr), this.maxDpr);

		this.canvas.width = width * this.dpr;
		this.canvas.height = height * this.dpr;
		this.canvas.style.width = `${ width }px`;
		this.canvas.style.height = `${ height }px`;

		return this.viewport(xOrig, yOrig, width, height);
	}

	// webglcontextlost
}
