/*
eslint-disable
linebreak-style,
id-match,
no-magic-numbers,
id-length,
no-underscore-dangle
*/

export default function ({
  Vec3,
  Quat,
  Mat4,
})
{
  const origin = {
    quaternion: new Quat(),
    matrix: new Mat4(),
  };

  const X = new Vec3().x(1);
  const Y = new Vec3().y(1);
  const Z = new Vec3().z(1);

  class Obj {
    constructor() {
      this.class = 'Obj';
      this.parent = origin;
      this.origin = new Vec3();
      this.scale = new Vec3();
      this.quaternion = new Quat();
      this.translation = new Vec3();
      this.matrix = new Mat4();

      this.mat = new Mat4();
      this.vec = new Vec3();
      // this.quat_ = new Quat();
    }

    copy(obj) {
      this.origin.copy(obj.origin);
      this.scale.copy(obj.scale);
      this.quaternion.copy(obj.quaternion);
      this.translation.copy(obj.translation);
      this.matrix.copy(obj.matrix);

      return this;
    }

    clone() {
      const obj = new Obj();

      obj.parent = this.parent;
      obj.origin.copy(this.origin);
      obj.scale.copy(this.scale);
      obj.quaternion.copy(this.quaternion);
      obj.translation.copy(this.translation);
      obj.matrix.copy(this.matrix);

      return obj;
    }

    attach(obj) {
      this.parent = obj;

      return this;
    }

    detach() {
      this.parent = origin;

      return this;
    }

    // scale(axis, angle) {
    //   this.scaleMatrix.scale(axis, angle);
    // }

    // scaleXYZ(scale) {
    //   this.scaleMatrix.scaleXYZ(scale);
    // }

    // scaleX(value) {
    //   this.scaleMatrix.scaleX(value);
    // }

    // scaleY(value) {
    //   this.scaleMatrix.scaleY(value);
    // }

    // scaleZ(value) {
    //   this.scaleMatrix.scaleZ(value);
    // }

    setRotation(axis, angle) {
      this.quaternion.makeRotation(axis, angle);

      return this;
    }

    setRotationX(angle) {
      this.quaternion.makeRotationX(angle);

      return this;
    }

    setRotationY(angle) {
      this.quaternion.makeRotationY(angle);

      return this;
    }

    setRotationZ(angle) {
      this.quaternion.makeRotationZ(angle);

      return this;
    }

    preRotate(axis, angle) {
      this.quaternion.preRotate(axis, angle);

      return this;
    }

    preRotateX(angle) {
      this.quaternion.preRotateX(angle);

      return this;
    }

    preRotateY(angle) {
      this.quaternion.preRotateY(angle);

      return this;
    }

    preRotateZ(angle) {
      this.quaternion.preRotateZ(angle);

      return this;
    }

    postRotate(axis, angle) {
      this.quaternion.postRotate(axis, angle);

      return this;
    }

    postRotateX(angle) {
      this.quaternion.postRotateX(angle);

      return this;
    }

    postRotateY(angle) {
      this.quaternion.postRotateY(angle);

      return this;
    }

    postRotateZ(angle) {
      this.quaternion.postRotateZ(angle);

      return this;
    }

    getDirectionX() {
      return new Vec3().copy(X).applyQuat(this.quaternion);
    }

    // getGlobalDirectionX() {
    //   return new Vec3().copy(X).applyQuat(this.globalQuat);
    // }

    setDirectionX(vec) {
      this.vec
        .cross(X, vec)
        .normalize();

      return this.setRotation(this.vec, X.angle(vec));
    }

    getDirectionY() {
      return new Vec3().copy(Y).applyQuat(this.quaternion);
    }

    // getGlobalDirectionY() {
    //   return new Vec3().copy(Y).applyQuat(this.globalQuat);
    // }

    setDirectionY(vec) {
      this.vec
        .cross(Y, vec)
        .normalize();

      return this.setRotation(this.vec, Y.angle(vec));
    }

    getDirectionZ() {
      return new Vec3().copy(Z).applyQuat(this.quaternion);
    }

    // getGlobalDirectionZ() {
    //   return new Vec3().copy(Z).applyQuat(this.globalQuat);
    // }

    setDirectionZ(vec) {
      this.vec
        .cross(Z, vec)
        .normalize();

      return this.setRotation(this.vec, Z.angle(vec));
    }

    setBackDirectionZ(vec) {
      this.vec
        .cross(Z, vec)
        .normalize();

      return this.setRotation(this.vec, Math.PI + Z.angle(vec));
    }

    setTranslation(axis, value) {
      this.translation.copy(axis);
      this.translation.mulS(value);

      return this;
    }

    setTranslationX(value) {
      this.translation.set(value, 0, 0);

      return this;
    }

    setTranslationY(value) {
      this.translation.set(0, value, 0);

      return this;
    }

    setTranslationZ(value) {
      this.translation.set(0, 0, value);

      return this;
    }

    translate(axis, value) {
      // const a = this.translation.arr;
      // const aa = axis.arr;

      // a[0] += aa[0] * value;
      // a[1] += aa[1] * value;
      // a[2] += aa[2] * value;

      this.translation.add(this.vec.copy(axis).mulS(value));

      return this;
    }

    translateX(value) {
      // this.translation.arr[0] += value;
      this.translation.addX(value);

      return this;
    }

    translateY(value) {
      // this.translation.arr[1] += value;
      this.translation.addY(value);

      return this;
    }

    translateZ(value) {
      // this.translation.arr[2] += value;
      this.translation.addZ(value);

      return this;
    }

    translateGlobal(axis, value) {
      // this.quat_
      //   .copy(this.parent.quaternion)
      //   .conjugate();

      // this.mat.makeRotationQuat(this.quat_);

      this.mat
        .copy(this.parent.matrix)
        .extractRotation()
        .inverse();

      this.vec
        .copy(axis)
        .applyMat4(this.mat);

      return this.translate(this.vec, value);
    }

    setGlobalOrigin(vec) {
      this.origin.extractTranslation(this.matrix).sub(vec);

      return this;
    }

    update() {
      this.matrix
        .makeTranslation(this.origin, -1)
        .preRotateQuat(this.quaternion)
        .preTranslate(this.origin, 1)
        .preTranslate(this.translation, 1)
        .premultiply(this.parent.matrix);

      return this;
    }

    update2() {
      this.matrix
        .makeTranslation(this.translation, 1)
        .preRotateQuat(this.quaternion);

      return this;
    }

    // update() {
    //   this.globalQuat
    //     .copy(this.quaternion)
    //     .premultiply(this.parent.globalQuat);

    //   this.globalTranslation
    //     .copy(this.translation)
    //     .applyQuat(this.parent.globalQuat)
    //     .add(this.parent.globalTranslation);

    //   this.matrix
    //     .makeTranslation(this.origin, -1)
    //     .preRotateQuat(this.globalQuat)
    //     .preTranslate(this.origin, 1)
    //     .preTranslate(this.globalTranslation, 1);

    //   this.matrix
    //     .makeTranslation(this.origin, -1)
    //     .makeRotationQuat(this.quaternion)
    //     .preTranslate(this.origin, 1)
    //     .preTranslate(this.translation, 1)
    //     .premultiply(this.parent.matrix);

    //   return this;
    // }

    $() {
      this.update();

      this.matrix.$();

      return this;
    }

    // update with all attached objects
  }

  return Obj;
};
