// rename pixel to texel or smtn else
// add more options

export default function (

	{
		webgl_rendering_context,
		Mat4,
		Obj,
		Camera,
		Texture2D,
		Renderbuffer,
		Framebuffer,
		Shader,
		Program,
		VertexAttrib,
	},
)
{
	const gl = webgl_rendering_context;

	class Raycaster
	{
		constructor (options = {})
		{
			this.camera = options.camera || new Camera().setOrthographicProjection(-0.5, 0.5, 0.5, -0.5, 1, 2000, 1);
			this.cameraObj = new Obj();
			this.modelMatrix = options.modelMatrix || new Mat4();
			this.texture_type = options.texture_type || gl.FLOAT;
			this.pixel = this.texture_type === gl.FLOAT ? new Float32Array(4) : new Uint8Array(4);

			// complement with all possible parameters
			this.lastViewportValue = null;
			this.lastClearColorValue = null;
			this.lastBlendValue = null;

			this.texture2D = new Texture2D();
			Texture2D
				.bind(this.texture2D)
				.image2D2(0, gl.RGBA, 1, 1, 0, gl.RGBA, this.texture_type, null)
				.unbind();

			this.renderbuffer = new Renderbuffer();
			Renderbuffer
				.bind(this.renderbuffer)
				.storage(gl.DEPTH_COMPONENT16, 1, 1)
				.unbind();

			this.framebuffer = new Framebuffer();
			Framebuffer
				.bind(this.framebuffer)
				.texture2D(gl.COLOR_ATTACHMENT0, this.texture2D)
				.renderbuffer(gl.DEPTH_ATTACHMENT, this.renderbuffer)
				.unbind();

			// remove
			this.program = (options.program || new Program({
				VS: () => `
					${ Shader.maxPrecision.vs.float }
					${ VertexAttrib.inject('vertex', 0) }
					${ this.camera.projectionMatrix.inject('projectionMatrix') }
					${ this.camera.viewMatrix.inject('viewMatrix') }
					${ this.modelMatrix.inject('modelMatrix') }
					varying vec4 color;
					${ Shader.consts.FLOAT_TO_RGBA }
					void main() {
						vec4 coords = viewMatrix * modelMatrix * vec4(vertex, 1.0);
						color = FLOAT_TO_RGBA(-coords.z);
						gl_Position = projectionMatrix * coords;
					}`,

				FS: `
					${ Shader.maxPrecision.fs.float }
					varying vec4 color;
					void main() {
						gl_FragColor = color;
					}`,
			}));
		}

		// rename
		set (point, direction)
		{
			this.lastViewportValue = gl.getParameter(gl.VIEWPORT);
			this.lastClearColorValue = gl.getParameter(gl.COLOR_CLEAR_VALUE);
			this.lastBlendValue = gl.getParameter(gl.BLEND);

			this.cameraObj
				.setBackDirectionZ(direction)
				.setTranslation(point, 1)
				.update();

			this.camera.viewMatrix
				.copy(this.cameraObj.matrix)
				.inverse();

			this.framebuffer.bind();

			gl.viewport(0, 0, 1, 1);
			gl.clearColor(0, 0, 0, 1);
			gl.disable(gl.BLEND);

			this.program
				.use()
				.updateUniform('viewMatrix');

			return this;
		}

		set2 (point, direction)
		{
			this.lastViewportValue = gl.getParameter(gl.VIEWPORT);
			this.lastClearColorValue = gl.getParameter(gl.COLOR_CLEAR_VALUE);
			this.lastBlendValue = gl.getParameter(gl.BLEND);

			this.cameraObj
				.setBackDirectionZ(direction)
				.setTranslation(point, 1)
				.update();

			this.camera.viewMatrix
				.copy(this.cameraObj.matrix)
				.inverse();

			this.framebuffer.bind();

			gl.viewport(0, 0, 1, 1);
			gl.clearColor(0, 0, 0, 1);
			gl.disable(gl.BLEND);

			this.program
				.use3()
				.updateUniform('viewMatrix');

			return this;
		}

		set3 (point, direction)
		{
			this.lastViewportValue = gl.getParameter(gl.VIEWPORT);
			this.lastClearColorValue = gl.getParameter(gl.COLOR_CLEAR_VALUE);
			this.lastBlendValue = gl.getParameter(gl.BLEND);

			this.cameraObj
				.setBackDirectionZ(direction)
				.setTranslation(point, 1)
				.update();

			this.camera.viewMatrix
				.copy(this.cameraObj.matrix)
				.inverse();

			this.framebuffer.bind();

			gl.viewport(0, 0, 1, 1);
			gl.clearColor(0, 0, 0, 1);
			gl.disable(gl.BLEND);

			return this;
		}

		setModelMatrix (modelMatrix)
		{
			this.modelMatrix.copy(modelMatrix);

			this.program.updateUniform('modelMatrix');

			return this;
		}

		snap (clbk)
		{
			gl.clear(gl.COLOR_BUFFER_BIT | gl.DEPTH_BUFFER_BIT);

			clbk(this);

			// gl.finish();

			// glReadPixels call includes glFinish ?
			gl.readPixels(0, 0, 1, 1, gl.RGBA, this.texture_type, this.pixel);

			return this;
		}

		snap2 (x, y, width, height, type, clbk)
		{
			gl.clear(gl.COLOR_BUFFER_BIT | gl.DEPTH_BUFFER_BIT);

			clbk();

			// gl.finish();

			gl.readPixels(x, y, width, height, gl.RGBA, type, this.pixel);

			return this;
		}

		snap3 (clbk)
		{
			gl.clear(gl.COLOR_BUFFER_BIT | gl.DEPTH_BUFFER_BIT);

			clbk();

			// gl.finish();

			gl.readPixels(0, 0, 1, 1, gl.RGBA, this.texture_type, this.pixel);

			return this;
		}

		unset ()
		{
			Framebuffer.unbind();

			gl.viewport(...this.lastViewportValue);
			gl.clearColor(...this.lastClearColorValue);

			if (this.lastBlendValue) {
				gl.enable(gl.BLEND);
			}

			return this;
		}

		static RGBA2FLOAT(RGBA) {
			return Math.pow(2, RGBA[0] - 150) * (((RGBA[1] % 128) * 65536) + (RGBA[2] * 256) + RGBA[3] + 0x800000);
		}

		RGBA2FLOAT() {
			return Raycaster.gl.RGBA2gl.FLOAT(this.pixel);
		}
	}

	return Raycaster;
}
