export default function (

	{
		webgl_rendering_context,
		Shader,
	},
)
{
	const gl = webgl_rendering_context;

	class Stack
	{
		constructor ()
		{
			this.length = 0;

			// rename to elements ro smth
			this.arr = [];
		}

		extend ()
		{
			this.arr.push(null);
		}

		push (_function)
		{
			this.arr[this.length] = _function;

			++this.length;
		}
	}

	class Program
	{
		static currently_being_created = null;

		static unuse ()
		{
			gl.useProgram(null);

			// return Program;
		}

		static getShader (code, type)
		{
			const shader = new Shader(type);

			shader.source(code);
			shader.compile();
			shader.checkCompileStatus(code);

			return shader;
		}

		constructor (options)
		{
			Program.currently_being_created = this;



			this.handle = gl.createProgram();

			// this.VS = options.VS;
			// this.FS = options.FS;

			this.uniforms = {};
			this.uniform_locations = {};
			this.uniformUpdateFunctionsArray = [];
			this.uniformUpdateFunctionsObject = {};
			this.uniform_stack = new Stack();

			this.beforeLinkingActions = [];
			this.afterLinkingActions = [];



			const vertexShader = (typeof options.VS === 'function' ? options.VS(this) : options.VS);
			const fragmentShader = (typeof options.FS === 'function' ? options.FS(this) : options.FS);

			this.attachShader(Program.getShader(vertexShader, gl.VERTEX_SHADER));
			this.attachShader(Program.getShader(fragmentShader, gl.FRAGMENT_SHADER));
			this.onBeforeLinking();
			this.link();
			this.checkLinkStatus();
			this.use();
			this.onAfterLinking();
			this.unuse();

			// return this;
		}

		use ()
		{
			gl.useProgram(this.handle);

			// return this;
		}

		use2 ()
		{
			gl.useProgram(this.handle);

			this.updateUniforms();
		}

		use3 ()
		{
			gl.useProgram(this.handle);

			this.updateUniformStackUniforms();
		}

		unuse ()
		{
			gl.useProgram(null);

			// return this;
		}

		attachShader (shader)
		{
			gl.attachShader(this.handle, shader.handle);

			// return this;
		}

		link ()
		{
			gl.linkProgram(this.handle);

			// return this;
		}

		bindAttribLocation (location, vertexAttrib)
		{
			gl.bindAttribLocation(this.handle, location, vertexAttrib);

			// return this;
		}

		getAttribLocation (vertexAttrib)
		{
			return gl.getAttribLocation(this.handle, vertexAttrib);

			// return this;
		}

		getUniformLocation (uniform)
		{
			return gl.getUniformLocation(this.handle, uniform);
		}

		updateUniform (name)
		{
			this.uniformUpdateFunctionsObject[name]();

			// return this;
		}

		updateUniforms ()
		{
			const len = this.uniformUpdateFunctionsArray.length;

			for (let i = 0; i < len; ++i)
			{
				const _function = this.uniformUpdateFunctionsArray[i];

				_function();
			}

			// return this;
		}

		updateUniformStackUniforms ()
		{
			const len = this.uniform_stack.length;

			this.uniform_stack.length = 0;

			for (let i = 0; i < len; ++i)
			{
				const _function = this.uniform_stack.arr[i];

				_function();
			}

			// return this;
		}

		getParameter (param)
		{
			return gl.getProgramParameter(this.handle, param);
		}

		getInfoLog ()
		{
			return gl.getProgramInfoLog(this.handle);
		}

		checkLinkStatus ()
		{
			if (!this.getParameter(gl.LINK_STATUS))
			{
				throw new Error(`${ gl.LINK_STATUS }, ${ this.getInfoLog() }`);
			}

			// return this;
		}

		onBeforeLinking ()
		{
			this.beforeLinkingActions.forEach((_) => _());

			// return this;
		}

		onAfterLinking ()
		{
			this.afterLinkingActions.forEach((_) => _());

			// return this;
		}

		// // remove
		// compose ()
		// {
		// 	// LOG(this)
		// 	// Program.currently_being_created = this;

		// 	// const vertexShader = (typeof this.VS === 'function' ? this.VS(this) : this.VS);
		// 	// const fragmentShader = (typeof this.FS === 'function' ? this.FS(this) : this.FS);

		// 	// this.attachShader(Program.getShader(vertexShader, gl.VERTEX_SHADER));
		// 	// this.attachShader(Program.getShader(fragmentShader, gl.FRAGMENT_SHADER));
		// 	// this.onBeforeLinking();
		// 	// this.link();
		// 	// this.checkLinkStatus();
		// 	// this.use();
		// 	// this.onAfterLinking();
		// 	// this.unuse();

		// 	// return this
		// 	// 	.attachShader(Program.getShader(vertexShader, gl.VERTEX_SHADER))
		// 	// 	.attachShader(Program.getShader(fragmentShader, gl.FRAGMENT_SHADER))
		// 	// 	.onBeforeLinking()
		// 	// 	.link()
		// 	// 	.checkLinkStatus()
		// 	// 	.use()
		// 	// 	.onAfterLinking()
		// 	// 	.unuse();

		// 	return this;
		// }

		delete ()
		{
			gl.deleteProgram(this.handle);

			// return this;
		}
	}

	return Program;
}
