/*
eslint-disable
linebreak-style,
id-match
*/

export default function ({
  webgl_rendering_context,
})
{
  const gl = webgl_rendering_context;

  const {
    FRAMEBUFFER,
    RENDERBUFFER,
    TEXTURE_2D,
  } = gl;

  class Framebuffer {
    constructor() {
      this.class = 'Framebuffer';
      this.handle = gl.createFramebuffer();
    }

    bind() {
      gl.bindFramebuffer(FRAMEBUFFER, this.handle);
      return this;
    }

    // READ_FRAMEBUFFER
    // DRAW_FRAMEBUFFER
    bind2(target) {
      gl.bindFramebuffer(target, this.handle);
      return this;
    }

    unbind() {
      gl.bindFramebuffer(FRAMEBUFFER, null);
      return this;
    }

    texture2D(attachment, texture2D) {
      gl.framebufferTexture2D(FRAMEBUFFER, attachment, TEXTURE_2D, texture2D.handle, 0);
      return this;
    }

    renderbuffer(attachment, renderbuffer) {
      gl.framebufferRenderbuffer(FRAMEBUFFER, attachment, RENDERBUFFER, renderbuffer.handle);
      return this;
    }

    static bind(framebuffer) {
      gl.bindFramebuffer(FRAMEBUFFER, framebuffer.handle);
      return Framebuffer;
    }

    static unbind() {
      gl.bindFramebuffer(FRAMEBUFFER, null);
      return Framebuffer;
    }

    static texture2D(attachment, texture2D) {
      gl.framebufferTexture2D(FRAMEBUFFER, attachment, TEXTURE_2D, texture2D.handle, 0);
      return Framebuffer;
    }

    static texture2D2(attachment, target, texture2D) {
      gl.framebufferTexture2D(FRAMEBUFFER, attachment, target, texture2D.handle, 0);
      return Framebuffer;
    }

    static renderbuffer(attachment, renderbuffer) {
      gl.framebufferRenderbuffer(FRAMEBUFFER, attachment, RENDERBUFFER, renderbuffer.handle);
      return Framebuffer;
    }

    delete() {
      gl.deleteFramebuffer(this.handle);
      return this;
    }
  }

  return Framebuffer;
};
