export default function (

	{
		Mat4,
		UniformSet,
	},
)
{
	class Camera
	{
		constructor (useUniformSet = false)
		{
			this.class = 'Camera';
			this.viewMatrix = new Mat4();
			this.projectionMatrix = new Mat4();
			this.projection_view_matrix = new Mat4();

			if (useUniformSet)
			{
				this.uniform_set =
					new UniformSet(

						{
							view_matrix:
							{
								target: this.viewMatrix,

								offset: 0,
							},

							projection_matrix:
							{
								target: this.projectionMatrix,

								offset: 16 * 4,
							},
						},
					);
			}
		}

		setPerspectiveProjection (fov, aspect, near, far, zoom)
		{
			const top = near * Math.tan(0.017453292 * 0.5 * fov) / zoom;
			const height = 2 * top;
			const width = aspect * height;
			const left = -0.5 * width;

			this.projectionMatrix.makePerspectiveProjection(left, left + width, top, top - height, near, far);

			return this;
		}

		setOrthographicProjection (left, right, top, bottom, near, far, zoom)
		{
			const dx = (right - left) / (2 * zoom);
			const dy = (top - bottom) / (2 * zoom);
			const cx = (right + left) / 2;
			const cy = (top + bottom) / 2;
			const lx = cx - dx;
			const rx = cx + dx;
			const ty = cy + dy;
			const by = cy - dy;

			this.projectionMatrix.makeOrthographicProjection(lx, rx, ty, by, near, far);

			return this;
		}

		$ ()
		{
			this.viewMatrix.$();
			this.projectionMatrix.$();
			this.projection_view_matrix.$();

			return this;
		}

		// get view matrix by inverting transform matrix
	}

	return Camera;
}
