export default function ({
	webgl_rendering_context,
	Shader,
	Program,
	VertexAttrib,
	VertexBuffer,
	IndexBuffer,
	Vec1,
	Vec2,
	Vec3,
	// Quat,
	Mat4,
	Obj,
})
{
	const gl = webgl_rendering_context;

	const {
		TRIANGLES,
		UNSIGNED_SHORT,
	} = gl;

	const mat = new Mat4();
	const mat2 = new Mat4();

	const X = new Vec3().x(1);
	const Y = new Vec3().y(1);

	// rename
	const qwe = new Vec3();

	const getConeGeometry = (radius, height, radialSegments, translation, matrix) => {
		height = height || 1;

		radialSegments = Math.floor(radialSegments) || 8;
		const heightSegments = 1;

		const thetaStart = 0;
		const thetaLength = Math.PI * 2;

		const indices = [];
		const vertices = [];

		let index = 0;
		const indexArray = [];
		const halfHeight = height / 2;

		const generateTorso = () => {
			const vertex = new Vec3();

			for (let y = 0; y <= heightSegments; y++) {
				const indexRow = [];

				const v = y / heightSegments;

				const _radius = v * radius;

				for (let x = 0; x <= radialSegments; x++) {
					const u = x / radialSegments;

					const theta = (u * thetaLength) + thetaStart;

					const sinTheta = Math.sin(theta);
					const cosTheta = Math.cos(theta);

					vertex.arr[0] = _radius * sinTheta;
					vertex.arr[1] = -(v * height) + halfHeight;
					vertex.arr[2] = _radius * cosTheta;

					if (translation < 0) {
						vertex.applyMat4(mat2.makeRotationX(Math.PI));
					}

					vertex.applyMat4(mat2.makeTranslationY(translation));

					if (matrix) {
						vertex.applyMat4(matrix);
					}

					vertices.push(vertex.arr[0], vertex.arr[1], vertex.arr[2]);

					indexRow.push(index++);
				}

				indexArray.push(indexRow);
			}

			if (translation < 0) {
				for (let x = 0; x < radialSegments; x++) {
					for (let y = 0; y < heightSegments; y++) {
						const a = indexArray[y][x];
						const b = indexArray[y + 1][x];
						const c = indexArray[y + 1][x + 1];
						const d = indexArray[y][x + 1];

						indices.push(a, b, d);
						indices.push(b, c, d);
					}
				}
			}
		};

		generateTorso();

		return { indices, vertices };
	};

	const getCylinderGeometry = (radius, height, radialSegments, matrix) => {
		height = height || 1;

		radialSegments = Math.floor(radialSegments) || 8;
		const heightSegments = 1;

		const thetaStart = 0;
		const thetaLength = Math.PI * 2;

		const indices = [];
		const vertices = [];

		let index = 0;
		const indexArray = [];
		const halfHeight = height / 2;

		const generateTorso = () => {
			const vertex = new Vec3();

			for (let y = 0; y <= heightSegments; y++) {
				const indexRow = [];

				const v = y / heightSegments;

				for (let x = 0; x <= radialSegments; x++) {
					const u = x / radialSegments;

					const theta = (u * thetaLength) + thetaStart;

					const sinTheta = Math.sin(theta);
					const cosTheta = Math.cos(theta);

					vertex.arr[0] = radius * sinTheta;
					vertex.arr[1] = -(v * height) + halfHeight;
					vertex.arr[2] = radius * cosTheta;

					if (matrix) {
						vertex.applyMat4(matrix);
					}

					vertices.push(vertex.arr[0], vertex.arr[1], vertex.arr[2]);

					indexRow.push(index++);
				}

				indexArray.push(indexRow);
			}

			if (!matrix) {
				for (let x = 0; x < radialSegments; x++) {
					for (let y = 0; y < heightSegments; y++) {
						const a = indexArray[y][x];
						const b = indexArray[y + 1][x];
						const c = indexArray[y + 1][x + 1];
						const d = indexArray[y][x + 1];

						indices.push(a, b, d);
						indices.push(b, c, d);
					}
				}
			}
		};

		generateTorso();

		const cone1 = getConeGeometry(radius * 3, radius / 0.5 * 6, 32, -height * 0.5, matrix);
		const cone2 = getConeGeometry(radius * 3, radius / 0.5 * 6, 32, height * 0.5, matrix);

		return {
			indices: indices
				.concat(cone1.indices.map((elm) => (elm + (vertices.length / 3))))
				.concat(cone1.indices.map((elm) => (elm + (vertices.length / 3) + (cone1.vertices.length / 3)))),

			vertices: vertices
				.concat(cone1.vertices)
				.concat(cone2.vertices),
		};
	};

	const getTorusGeometry = (radius, tube, radialSegments, tubularSegments, arc, matrix) => {
		radius = radius || 1;
		tube = tube || 0.4;
		radialSegments = Math.floor(radialSegments) || 8;
		tubularSegments = Math.floor(tubularSegments) || 6;
		arc = arc || Math.PI * 2;

		const indices = [];
		const vertices = [];

		const vertex = new Vec3();

		for (let j = 0; j <= radialSegments; j++) {
			for (let i = 0; i <= tubularSegments; i++) {
				const u = i / tubularSegments * arc;
				const v = j / radialSegments * Math.PI * 2;

				vertex.arr[0] = (radius + (tube * Math.cos(v))) * Math.cos(u);
				vertex.arr[1] = (radius + (tube * Math.cos(v))) * Math.sin(u);
				vertex.arr[2] = tube * Math.sin(v);

				if (matrix) {
					vertex.applyMat4(matrix);
				}

				vertices.push(vertex.arr[0], vertex.arr[1], vertex.arr[2]);
			}
		}

		if (!matrix) {
			for (let j = 1; j <= radialSegments; j++) {
				for (let i = 1; i <= tubularSegments; i++) {
					const a = ((tubularSegments + 1) * j) + i - 1;
					const b = ((tubularSegments + 1) * (j - 1)) + i - 1;
					const c = ((tubularSegments + 1) * (j - 1)) + i;
					const d = ((tubularSegments + 1) * j) + i;

					indices.push(a, b, d);
					indices.push(b, c, d);
				}
			}
		}

		return { indices, vertices };
	};

	class Gumball {
		constructor(scale, camera, translationFactor = 1, modelOptions = {}) {
			this.translationFactor = translationFactor;
			this.actionIndex = 0;
			// this.angle = 0;
			this.value = 0;
			this.obj = new Obj();
			this.axis = new Vec3();
			this.axis2 = new Vec3();
			this.axis3 = new Vec3();
			this.prevTangentPoint = new Vec3();
			this.tangentPoint = new Vec3();
			this.tangentDir = new Vec3();
			this.tangentDirScreen = new Vec2();
			this.modelOrigin = new Vec3();
			this.modelOriginProjection = new Vec3();
			this.movementDir = new Vec2();
			this.camera = camera;

			this.cyl_attr1 = getCylinderGeometry(modelOptions.lineThickness || 0.5, modelOptions.lineLength || 200, 6, mat.makeRotationZ(Math.PI * 0.5));
			this.cyl_attr2 = getCylinderGeometry(modelOptions.lineThickness || 0.5, modelOptions.lineLength || 200, 6);
			this.cyl_attr3 = getCylinderGeometry(modelOptions.lineThickness || 0.5, modelOptions.lineLength || 200, 6, mat.makeRotationX(Math.PI * 0.5));

			this.torus_attr1 = getTorusGeometry(modelOptions.radius || 50, modelOptions.circleThickness || 0.5, 6, 32, 6.3, mat.makeRotationY(Math.PI * 0.5));
			this.torus_attr2 = getTorusGeometry(modelOptions.radius || 50, modelOptions.circleThickness || 0.5, 6, 32, 6.3, mat.makeRotationX(Math.PI * 0.5));
			this.torus_attr3 = getTorusGeometry(modelOptions.radius || 50, modelOptions.circleThickness || 0.5, 6, 32, 6.3);

			const vertex_data = [];
			const index_data = [];
			this.index_data = index_data;

			for (let i = 0; i < this.cyl_attr2.indices.length; ++i) {
				index_data.push(

					(vertex_data.length / 4) +
					this.cyl_attr2.indices[i],
				);
			}

			for (let i = 0; i < this.cyl_attr1.vertices.length; i += 3) {
				vertex_data.push(

					this.cyl_attr1.vertices[i + 0],
					this.cyl_attr1.vertices[i + 1],
					this.cyl_attr1.vertices[i + 2],
					0
				);
			}

			for (let i = 0; i < this.cyl_attr2.indices.length; ++i) {
				index_data.push(

					(vertex_data.length / 4) +
					this.cyl_attr2.indices[i],
				);
			}

			for (let i = 0; i < this.cyl_attr2.vertices.length; i += 3) {
				vertex_data.push(

					this.cyl_attr2.vertices[i + 0],
					this.cyl_attr2.vertices[i + 1],
					this.cyl_attr2.vertices[i + 2],
					1
				);
			}

			for (let i = 0; i < this.cyl_attr2.indices.length; ++i) {
				index_data.push(

					(vertex_data.length / 4) +
					this.cyl_attr2.indices[i],
				);
			}

			for (let i = 0; i < this.cyl_attr3.vertices.length; i += 3) {
				vertex_data.push(

					this.cyl_attr3.vertices[i + 0],
					this.cyl_attr3.vertices[i + 1],
					this.cyl_attr3.vertices[i + 2],
					2
				);
			}

			for (let i = 0; i < this.torus_attr3.indices.length; ++i) {
				index_data.push(

					(vertex_data.length / 4) +
					this.torus_attr3.indices[i],
				);
			}

			for (let i = 0; i < this.torus_attr1.vertices.length; i += 3) {
				vertex_data.push(

					this.torus_attr1.vertices[i + 0],
					this.torus_attr1.vertices[i + 1],
					this.torus_attr1.vertices[i + 2],
					3
				);
			}

			for (let i = 0; i < this.torus_attr3.indices.length; ++i) {
				index_data.push(

					(vertex_data.length / 4) +
					this.torus_attr3.indices[i],
				);
			}

			for (let i = 0; i < this.torus_attr2.vertices.length; i += 3) {
				vertex_data.push(

					this.torus_attr2.vertices[i + 0],
					this.torus_attr2.vertices[i + 1],
					this.torus_attr2.vertices[i + 2],
					4
				);
			}

			for (let i = 0; i < this.torus_attr3.indices.length; ++i) {
				index_data.push(

					(vertex_data.length / 4) +
					this.torus_attr3.indices[i],
				);
			}

			for (let i = 0; i < this.torus_attr3.vertices.length; i += 3) {
				vertex_data.push(

					this.torus_attr3.vertices[i + 0],
					this.torus_attr3.vertices[i + 1],
					this.torus_attr3.vertices[i + 2],
					5
				);
			}

			this.vertex_buffer = new VertexBuffer().bind().data(new Float32Array(vertex_data));

			this.index_buffer = new IndexBuffer()
				.bind()
				.data(new Uint16Array(index_data));

			this.program = new Program({
				VS: () => `
					${ Shader.maxPrecision.vs.int }
					${ Shader.maxPrecision.vs.float }

					${ VertexAttrib.inject('vertex_data', 0, 'vec4') }
					${ this.camera.projection_view_matrix.inject('projection_view_matrix') }
					${ new Mat4().copy(this.obj.matrix).inject('model_matrix') }
					${ new Vec1().x(scale).inject('scale') }
					${ new Vec1(new Int32Array(6)).inject('color_index', 'int[]') }
					${ new Vec1(new Int32Array([ 1, 1, 1, 1, 1, 1 ])).inject('element_visibility', 'int[]') }
					${ new Vec3(new Float64Array([ 1, 0.75, 0.75, 0.75, 1, 0.75, 0.75, 0.75, 1, 1, 0.75, 0.75, 0.75, 1, 0.75, 0.75, 0.75, 1, 1, 0, 0, 0, 1, 0, 0, 0, 1, 1, 0, 0, 0, 1, 0, 0, 0, 1 ])).inject('color', 'vec3[]') }
					${ new Vec1().inject('visibility', 'int') }
					varying vec3 v_color;

					#define RAYCAST_MODE_OFF u_raycast_mode == 0
					uniform int u_raycast_mode;
					// uniform mat4 u_raycast_projection_matrix;
					uniform mat4 u_raycast_view_matrix;
					varying vec4 v_raycast_output;
					const mat4 u_raycast_projection_matrix =
						mat4(vec4(2.0, 0, 0, 0), vec4(0, 2.0, 0, 0), vec4(0, 0, -0.0010005002841353416, 0), vec4(0, 0, -1.0010005235671997, 1.0));

					void main() {

						vec3 position = vertex_data.xyz;
						float element_index = vertex_data.w;
						int element_index_int = int(element_index);

						vec4 world_position = model_matrix * vec4(position * scale, 1.0);

						if (RAYCAST_MODE_OFF)
						{
							gl_Position = projection_view_matrix * world_position;

							v_color = color[color_index[element_index_int] + element_index_int];
						}
						else
						{
							vec4 view_position = u_raycast_view_matrix * world_position;

							gl_Position = u_raycast_projection_matrix * view_position;

							// pass [ distance(1), index(1) ]
							v_raycast_output.rg = vec2(-view_position.z, element_index);
						}



						if (visibility == 0)
						{
							gl_Position.a = 0.0;
						}

						if (element_visibility[element_index_int] == 0)
						{
							gl_Position.a = 0.0;
						}

						// gl_Position *= visibility;
					}`,

				FS: `
					${ Shader.maxPrecision.fs.int }
					${ Shader.maxPrecision.fs.float }

					varying vec3 v_color;

					#define RAYCAST_MODE_OFF u_raycast_mode == 0
					uniform int u_raycast_mode;
					varying vec4 v_raycast_output;

					void main ()
					{
						if (RAYCAST_MODE_OFF)
						{
							gl_FragColor = vec4(v_color, 1.0);
						}
						else
						{
							gl_FragColor = v_raycast_output;
						}
					}`,
			});
		}

		bindObj(obj) {
			this.obj = obj;

			return this;
		}

		get2DAngleY(resolution = 1) {
			this.tangentDirScreen
				.copy(this.tangentDir)
				.mulX(resolution);

			return this.tangentDirScreen.angle(Y) / Math.PI * 180 * Math.sign(this.tangentDirScreen.dot(X));
		}

		setCircleTangent(point, direction) {
			this.tangentPoint
				.copy(direction)
				.intersectPlane(point, this.axis, this.modelOrigin)
				.sub(this.modelOrigin);

			if (isNaN(this.tangentPoint.arr[0] * this.tangentPoint.arr[1] * this.tangentPoint.arr[2])) {
				this.tangentPoint.reset();
			}

			this.tangentDir
				.copy(this.tangentPoint)
				.verticalize(this.axis)
				.add(this.modelOrigin)
				.project2(this.camera)
				.z(0)
				.sub(this.modelOriginProjection);

			return this;
		}

		setAxisTangent(point, direction) {
			// rename
			qwe
				.copy(this.modelOrigin)
				.add(this.axis)
				.sub(point)
				.normalize()
				.intersectPlane(point, this.axis, this.modelOrigin)
				.sub(this.modelOrigin)
				.normalize();

			this.tangentPoint
				.copy(direction)
				.intersectPlane(point, qwe, this.modelOrigin)
				.projectOnVector(this.axis);

			if (isNaN(this.tangentPoint.arr[0] * this.tangentPoint.arr[1] * this.tangentPoint.arr[2])) {
				this.tangentPoint.reset();
			}

			this.tangentDir
				.copy(this.modelOrigin)
				.add(this.axis)
				.project2(this.camera)
				.z(0)
				.sub(this.modelOriginProjection);

			return this;
		}

		rotate(x, y, f) {
			// this.angle = -this.prevTangentPoint.angle(this.tangentPoint) * Math.sign(this.movementDir.set(x, -y).dot(this.tangentDir)) * f;
			this.value = -this.prevTangentPoint.angle(this.tangentPoint) * Math.sign(this.movementDir.set(x, -y).dot(this.tangentDir)) * f;

			this.prevTangentPoint.copy(this.tangentPoint);

			// this.obj.preRotate(this.axis, this.angle);
			this.obj.preRotate(this.axis, this.value);

			return this;
		}

		translate(x, y, f) {
			this.value = this.prevTangentPoint.distance(this.tangentPoint) * Math.sign(this.movementDir.set(x, -y).dot(this.tangentDir)) * f;

			this.prevTangentPoint.copy(this.tangentPoint);

			this.obj.translateGlobal(this.axis, this.value);

			this.modelOriginProjection
				.copy(

					this.modelOrigin.extractTranslation(this.obj.matrix)
				)
				.project2(this.camera)
				.z(0);

			return this;
		}

		draw() {
			this.program.use3();

			gl.enableVertexAttribArray(0);

			this.program.use3();

			gl.enableVertexAttribArray(0);

			VertexAttrib.pointer2(this.vertex_buffer, 0, 4);
			this.index_buffer.bind();
			gl.drawElements(TRIANGLES, this.index_data.length, UNSIGNED_SHORT, 0);

			return this;
		}

		drawRaycastMode(raycaster) {
			this.program.use3();

			gl.uniform1i(this.program.getUniformLocation('u_raycast_mode'), 1);

			// gl.uniformMatrix4fv(

			// 	this.program.getUniformLocation('u_raycast_projection_matrix'),
			// 	false,
			// 	raycaster.camera.projectionMatrix.arr,
			// );

			gl.uniformMatrix4fv(

				this.program.getUniformLocation('u_raycast_view_matrix'),
				false,
				raycaster.camera.viewMatrix.arr,
			);

			VertexAttrib.pointer2(this.vertex_buffer, 0, 4);
			this.index_buffer.bind();
			gl.drawElements(TRIANGLES, this.index_data.length, UNSIGNED_SHORT, 0);

			gl.uniform1i(this.program.getUniformLocation('u_raycast_mode'), 0);

			// gl.uniformMatrix4fv(

			// 	this.program.getUniformLocation('projection_matrix'),
			// 	false,
			// 	this.camera.projectionMatrix.arr,
			// );

			// gl.uniformMatrix4fv(

			// 	this.program.getUniformLocation('view_matrix'),
			// 	false,
			// 	this.camera.viewMatrix.arr,
			// );

			return this;
		}

		highlight(element_index) {
			const color = [ 0, 0, 0, 0, 0, 0 ];

			if (element_index > -1 && element_index < 6) {
				color[element_index] = 6;
			}

			this.program.uniforms.color_index.arr.set(color);
			this.program.uniforms.color_index.$();
		}

		show(element_visibility = [ 0, 0, 0, 0, 0, 0 ]) {
			this.program.uniforms.element_visibility.arr.set(element_visibility);
			this.program.uniforms.element_visibility.$();
		}

		set(element_index, point, direction) {
			this.actionIndex = element_index;

			this.modelOriginProjection
				.copy(this.modelOrigin.extractTranslation(this.obj.matrix))
				.project2(this.camera)
				.z(0);

			if (element_index === 0 || element_index === 3) {
				this.axis.set(1, 0, 0);

			} else if (element_index === 1 || element_index === 4) {
				this.axis.set(0, 1, 0);
			} else if (element_index === 2 || element_index === 5) {
				this.axis.set(0, 0, 1);
			}

			this.axis.applyMat4Rotation(this.obj.matrix);

			if (element_index > 2) {
				this.setCircleTangent(point, direction);
			} else {
				this.setAxisTangent(point, direction);
			}

			this.prevTangentPoint.copy(this.tangentPoint);
		}

		transform(x, y, point, direction, f) {
			if (this.actionIndex > 2) {
				this
					.rotate(x, y, f)
					.setCircleTangent(point, direction);
			} else {
				this
					.translate(x, y, f)
					.setAxisTangent(point, direction);
			}

			return this;
		}

		reset() {
			this.prevTangentPoint.reset();
			this.tangentPoint.reset();
			this.program.uniforms.color_index.arr.set([ 0, 0, 0, 0, 0, 0 ]);
			this.program.uniforms.color_index.$();

			return this;
		}

		$() {
			this.obj.$();

			this.program.uniforms.model_matrix.copy(this.obj.matrix).$();

			return this;
		}
	}

	return Gumball;
};
