/*
eslint-disable
linebreak-style,
no-magic-numbers,
prefer-destructuring,
max-len,
id-length,
id-match,
array-element-newline
*/

export default function ({
  webgl_rendering_context,
  DataArrayConstructor,
  Program,
  $,
  // MAX_PROGRAM_UNIFORM_COUNT,
})
{
  const gl = webgl_rendering_context;

  const uniformArraySizeDivider = {
    'int[]': 1,

    'float[]': 1,

    'ivec4[]': 4,

    'vec4[]': 4,
  };

  const uniformCommands = {
    ivec4: (location, arr) => gl.uniform4i(location, arr[0], arr[1], arr[2], arr[3]),

    vec4: (location, arr) => gl.uniform4f(location, arr[0], arr[1], arr[2], arr[3]),

    'int[]': (location, arr) => gl.uniform1iv(location, arr),

    'float[]': (location, arr) => gl.uniform1fv(location, arr),

    'ivec4[]': (location, arr) => gl.uniform4iv(location, arr),

    'vec4[]': (location, arr) => gl.uniform4fv(location, arr),

    'float[i]': (i) => (location, arr) => gl.uniform1i(location, arr[i]),

    'int[i]': (i) => (location, arr) => gl.uniform1f(location, arr[i]),

    'ivec2[i]': (i) => (location, arr) => gl.uniform2i(location, arr[i], arr[i + 1]),

    'vec2[i]': (i) => (location, arr) => gl.uniform2f(location, arr[i], arr[i + 1]),

    'ivec3[i]': (i) => (location, arr) => gl.uniform3i(location, arr[i], arr[i + 1], arr[i + 2]),

    'vec3[i]': (i) => (location, arr) => gl.uniform3f(location, arr[i], arr[i + 1], arr[i + 2]),

    'ivec4[i]': (i) => (location, arr) => gl.uniform4i(location, arr[i], arr[i + 1], arr[i + 2], arr[i + 3]),

    'vec4[i]': (i) => (location, arr) => gl.uniform4f(location, arr[i], arr[i + 1], arr[i + 2], arr[i + 3]),
  };

  class Vec4 {
    constructor(arr) {
      this.class = 'Vec4';

      this.arr = arr || new DataArrayConstructor([ 0, 0, 0, 1 ]);

      this.containers = [];

      this.within_container_indices = [];

      this.container_count = 0;

      this.updated_container_count = 0;
    }

    x(x) {
      this.arr[0] = x;

      return this;
    }

    y(y) {
      this.arr[1] = y;

      return this;
    }

    z(z) {
      this.arr[2] = z;

      return this;
    }

    w(w) {
      this.arr[3] = w;

      return this;
    }

    set(x, y, z, w) {
      const a = this.arr;

      a[0] = x;
      a[1] = y;
      a[2] = z;
      a[3] = w;

      return this;
    }

    reset() {
      return this.set(0, 0, 0, 1);
    }

    copy(vec) {
      const va = vec.arr;

      return this.set(va[0], va[1], va[2], va[3]);
    }

    clone() {
      return new Vec4().copy(this);
    }

    negate() {
      const a = this.arr;

      a[0] *= -1;
      a[1] *= -1;
      a[2] *= -1;
      a[3] *= -1;

      return this;
    }

    add(vec) {
      const a = this.arr;
      const va = vec.arr;

      a[0] += va[0];
      a[1] += va[1];
      a[2] += va[2];
      a[3] += va[3];

      return this;
    }

    addX(value) {
      this.arr[0] += value;

      return this;
    }

    addY(value) {
      this.arr[1] += value;

      return this;
    }

    addZ(value) {
      this.arr[2] += value;

      return this;
    }

    addW(value) {
      this.arr[3] += value;

      return this;
    }

    addS(s) {
      const a = this.arr;

      a[0] += s;
      a[1] += s;
      a[2] += s;
      a[3] += s;

      return this;
    }

    addS2(x, y, z, w) {
      const a = this.arr;

      a[0] += x;
      a[1] += y;
      a[2] += z;
      a[3] += w;

      return this;
    }

    sub(vec) {
      const a = this.arr;
      const va = vec.arr;

      a[0] -= va[0];
      a[1] -= va[1];
      a[2] -= va[2];
      a[3] -= va[3];

      return this;
    }

    subX(value) {
      this.arr[0] -= value;

      return this;
    }

    subY(value) {
      this.arr[1] -= value;

      return this;
    }

    subZ(value) {
      this.arr[2] -= value;

      return this;
    }

    subW(value) {
      this.arr[2] -= value;

      return this;
    }

    subS(s) {
      const a = this.arr;

      a[0] -= s;
      a[1] -= s;
      a[2] -= s;
      a[3] -= s;

      return this;
    }

    subS2(x, y, z, w) {
      const a = this.arr;

      a[0] -= x;
      a[1] -= y;
      a[2] -= z;
      a[3] -= w;

      return this;
    }

    mul(vec) {
      const a = this.arr;
      const va = vec.arr;

      a[0] *= va[0];
      a[1] *= va[1];
      a[2] *= va[2];
      a[3] *= va[3];

      return this;
    }

    mulX(value) {
      this.arr[0] *= value;

      return this;
    }

    mulY(value) {
      this.arr[1] *= value;

      return this;
    }

    mulZ(value) {
      this.arr[2] *= value;

      return this;
    }

    mulW(value) {
      this.arr[3] *= value;

      return this;
    }

    mulS(s) {
      const a = this.arr;

      a[0] *= s;
      a[1] *= s;
      a[2] *= s;
      a[3] *= s;

      return this;
    }

    mulS2(x, y, z, w) {
      const a = this.arr;

      a[0] *= x;
      a[1] *= y;
      a[2] *= z;
      a[3] *= w;

      return this;
    }

    div(vec) {
      const a = this.arr;
      const va = vec.arr;

      a[0] /= va[0];
      a[1] /= va[1];
      a[2] /= va[2];
      a[3] /= va[3];

      return this;
    }

    divX(value) {
      this.arr[0] /= value;

      return this;
    }

    divY(value) {
      this.arr[1] /= value;

      return this;
    }

    divZ(value) {
      this.arr[2] /= value;

      return this;
    }

    divW(value) {
      this.arr[3] /= value;

      return this;
    }

    divS(s) {
      const a = this.arr;

      a[0] /= s;
      a[1] /= s;
      a[2] /= s;
      a[3] /= s;

      return this;
    }

    divS2(x, y, z, w) {
      const a = this.arr;

      a[0] /= x;
      a[1] /= y;
      a[2] /= z;
      a[3] /= w;

      return this;
    }

    lengthSquare() {
      const a = this.arr;

      return (a[0] * a[0]) + (a[1] * a[1]) + (a[2] * a[2]) + (a[3] * a[3]);
    }

    dot(vec3) {
      const a = this.arr;
      const va = vec3.arr;

      return (a[0] * va[0]) + (a[1] * va[1]) + (a[2] * va[2]) + (a[3] * va[3]);
    }

    length() {
      const a = this.arr;

      return Math.sqrt((a[0] * a[0]) + (a[1] * a[1]) + (a[2] * a[2]) + (a[3] * a[3]));
    }

    normalize() {
      const value = this.length() || 1;

      return this.divS(value);
    }

    makeFromHex(hex, alpha = 1) {
      return this.set(
        parseInt(`0x${ hex[0] }${ hex[1] }`, 16) / 255,
        parseInt(`0x${ hex[2] }${ hex[3] }`, 16) / 255,
        parseInt(`0x${ hex[4] }${ hex[5] }`, 16) / 255,
        alpha
      );
    }

    limitX(min, max) {
      this.arr[0] = Math.max(Math.min(this.arr[0], max), min);
    }

    limitY(min, max) {
      this.arr[1] = Math.max(Math.min(this.arr[1], max), min);
    }

    limitZ(min, max) {
      this.arr[2] = Math.max(Math.min(this.arr[2], max), min);
    }

    limitW(min, max) {
      this.arr[3] = Math.max(Math.min(this.arr[3], max), min);
    }

    get value() {
      return this.arr[0];
    }

    equals(m) {
      return (

        this.arr[0] === m.arr[0] &&
        this.arr[1] === m.arr[1] &&
        this.arr[2] === m.arr[2] &&
        this.arr[3] === m.arr[3]
      );
    }

    inject(name, type = 'vec4', location_ = 'auto') {
      const program = Program.currently_being_created;
      // const index_in_container = program.uniformCount++;
      const index_in_container = program.uniform_stack.arr.length;
      let line = `uniform ${ type.replace(/\[\]/, '').replace(/\[[0-9]+\]/g, '') } ${ name }`;
      let location = null;

      program.uniforms[name] = this;

      program.uniform_stack.extend();
      this.containers.push(program);
      this.within_container_indices[this.container_count++] = index_in_container;

      if (location_ === 'auto') {
        program.afterLinkingActions.push(() => {
          location = program.getUniformLocation(name);

          const index = type.match(/\[[0-9]+\]/g);
          const command = index ? uniformCommands[`${ type.replace(/\[[0-9]+\]/g, '') }[i]`](parseInt(index[0].replace(/\[|\]/g, ''), 10)) : uniformCommands[type];

          const updateFunction = () => {
            command(location, this.arr);
            this.updated_container_count = 0;
          };

          program.uniformUpdateFunctionsArray.push(updateFunction);
          program.uniformUpdateFunctionsObject[name] = updateFunction;

          updateFunction();
        });
      } else {
        location = location_;
        line = `layout (location = ${ location }) ${ line }`;
      }

      if (type.match(/\[\]/)) {
        line += `[${ this.arr.length / uniformArraySizeDivider[type] }]`;
      }

      return `${ line };`;
    }

    $() {
      // const len = this.container_count - this.updated_container_count;
      // this.updated_container_count = this.container_count;

      // for (let i = 0; i < len; i++) {
      //   const program = this.containers[i];
      //   program.uniform_stack.push(this.within_container_indices[i]);
      // }

      // const len = this.container_count - this.updated_container_count;
      // this.updated_container_count = this.container_count;

      if (this.updated_container_count === 0) {
        for (; this.updated_container_count < this.container_count; ++this.updated_container_count) {
          const program = this.containers[this.updated_container_count];
          // program.uniform_stack.push(this.within_container_indices[this.updated_container_count]);
          program.uniform_stack.push(program.uniformUpdateFunctionsArray[this.within_container_indices[this.updated_container_count]]);
        }

        $();
      }

      return this;
    }
  }

  return Vec4;
};
