/*
eslint-disable
linebreak-style,
id-match,
max-len,
max-params
*/

export default function ({
  webgl_rendering_context,
  textureUnits,
})
{
  const gl = webgl_rendering_context;

  class Texture {
    constructor() {
      this.class = 'Texture';
      this.handle = gl.createTexture();
    }

    bind(target) {
      gl.bindTexture(target, this.handle);

      return this;
    }

    unbind(target) {
      gl.bindTexture(target, null);

      return this;
    }

    unit(unit) {
      gl.activeTexture(textureUnits[unit]);

      return this;
    }

    image2D(target, level, internalFormat, format, type, image) {
      gl.texImage2D(target, level, internalFormat, format, type, image);

      return this;
    }

    image2D2(target, level, internalFormat, width, height, border, format, type, image) {
      gl.texImage2D(target, level, internalFormat, width, height, border, format, type, image);

      return this;
    }

    subImage2D(target, level, xoffset, yoffset, format, type, image) {
      gl.texSubImage2D(target, level, xoffset, yoffset, format, type, image);

      return this;
    }

    subImage2D2(target, level, xoffset, yoffset, width, height, format, type, image) {
      gl.texSubImage2D(target, level, xoffset, yoffset, width, height, format, type, image);

      return this;
    }

    mipmap(target) {
      gl.generateMipmap(target);

      return this;
    }

    parameteri(target, pname, param) {
      gl.texParameteri(target, pname, param);

      return this;
    }

    parameterf(target, pname, param) {
      gl.texParameterf(target, pname, param);

      return this;
    }

    delete() {
      gl.deleteTexture(this.handle);

      return this;
    }

    static bind(target, texture) {
      gl.bindTexture(target, texture.handle);

      return Texture;
    }

    static unbind(target) {
      gl.bindTexture(target, null);

      return Texture;
    }

    static unit(unit) {
      gl.activeTexture(textureUnits[unit]);

      return Texture;
    }

    static image2D(target, level, internalFormat, format, type, image) {
      gl.texImage2D(target, level, internalFormat, format, type, image);

      return Texture;
    }

    static image2D2(target, level, internalFormat, width, height, border, format, type, image) {
      gl.texImage2D(target, level, internalFormat, width, height, border, format, type, image);

      return Texture;
    }

    static subImage2D(target, level, xoffset, yoffset, format, type, image) {
      gl.texSubImage2D(target, level, xoffset, yoffset, format, type, image);

      return Texture;
    }

    static subImage2D2(target, level, xoffset, yoffset, width, height, format, type, image) {
      gl.texSubImage2D(target, level, xoffset, yoffset, width, height, format, type, image);

      return Texture;
    }

    static mipmap(target) {
      gl.generateMipmap(target);

      return Texture;
    }

    static parameteri(target, pname, param) {
      gl.texParameteri(target, pname, param);

      return Texture;
    }

    static parameterf(target, pname, param) {
      gl.texParameterf(target, pname, param);

      return Texture;
    }

    static delete(texture) {
      gl.deleteTexture(texture.handle);

      return Texture;
    }
  }

  return Texture;
};
