export default function (

	{
		webgl_rendering_context,
		Texture,
	},
)
{
	const gl = webgl_rendering_context;

	class Texture2D extends Texture
	{
		bind ()
		{
			Texture.bind(gl.TEXTURE_2D, this);

			return this;
		}

		unbind ()
		{
			Texture.unbind(gl.TEXTURE_2D);

			return this;
		}

		unit (unit)
		{
			Texture.unit(unit);

			return this;
		}

		image2D (level, internalFormat, format, type, image)
		{
			Texture.image2D(gl.TEXTURE_2D, level, internalFormat, format, type, image);

			return this;
		}

		image2D2 (level, internalFormat, width, height, border, format, type, image)
		{
			Texture.image2D2(gl.TEXTURE_2D, level, internalFormat, width, height, border, format, type, image);

			return this;
		}

		subImage2D(level, xoffset, yoffset, format, type, image) {
			Texture.subImage2D(gl.TEXTURE_2D, level, xoffset, yoffset, format, type, image);

			return this;
		}

		subImage2D2 (level, xoffset, yoffset, width, height, format, type, image)
		{
			Texture.subImage2D2(gl.TEXTURE_2D, level, xoffset, yoffset, width, height, format, type, image);

			return this;
		}

		mipmap ()
		{
			Texture.mipmap(gl.TEXTURE_2D);

			return this;
		}

		parameteri (pname, param)
		{
			Texture.parameteri(gl.TEXTURE_2D, pname, param);

			return this;
		}

		parameterf (pname, param)
		{
			Texture.parameterf(gl.TEXTURE_2D, pname, param);

			return this;
		}

		delete ()
		{
			Texture.delete(this);

			return this;
		}

		static bind (texture)
		{
			Texture.bind(gl.TEXTURE_2D, texture);

			return Texture2D;
		}

		static unbind() {
			Texture.unbind(gl.TEXTURE_2D);

			return Texture2D;
		}

		static unit (unit)
		{
			Texture.unit(unit);

			return Texture2D;
		}

		static image2D (level, internalFormat, format, type, image)
		{
			Texture.image2D(gl.TEXTURE_2D, level, internalFormat, format, type, image);

			return Texture2D;
		}

		static image2D2 (level, internalFormat, width, height, border, format, type, image)
		{
			Texture.image2D2(gl.TEXTURE_2D, level, internalFormat, width, height, border, format, type, image);

			return Texture2D;
		}

		static subImage2D (level, xoffset, yoffset, format, type, image)
		{
			Texture.subImage2D(gl.TEXTURE_2D, level, xoffset, yoffset, format, type, image);

			return Texture2D;
		}

		static subImage2D2 (level, xoffset, yoffset, width, height, format, type, image)
		{
			Texture.subImage2D2(gl.TEXTURE_2D, level, xoffset, yoffset, width, height, format, type, image);

			return Texture2D;
		}

		static mipmap ()
		{
			Texture.mipmap(gl.TEXTURE_2D);

			return Texture2D;
		}

		static parameteri (pname, param)
		{
			Texture.parameteri(gl.TEXTURE_2D, pname, param);

			return Texture2D;
		}

		static parameterf (pname, param)
		{
			Texture.parameterf(gl.TEXTURE_2D, pname, param);

			return Texture2D;
		}

		static delete (texture)
		{
			Texture.delete(texture);

			return Texture2D;
		}
	}

	return Texture2D;
}
