/*
eslint-disable
linebreak-style,
id-match
*/

export default function ({
  webgl_rendering_context,
})
{
  const gl = webgl_rendering_context;

  const {
    RENDERBUFFER,
  } = gl;

  class Renderbuffer {
    constructor() {
      this.class = 'Renderbuffer';
      this.handle = gl.createRenderbuffer();
    }

    bind() {
      gl.bindRenderbuffer(RENDERBUFFER, this.handle);
      return this;
    }

    unbind() {
      gl.bindRenderbuffer(RENDERBUFFER, null);
      return this;
    }

    storage(format, width, height) {
      gl.renderbufferStorage(RENDERBUFFER, format, width, height);
      return this;
    }

    delete() {
      gl.deleteRenderbuffer(this.handle);
      return this;
    }

    static bind(renderbuffer) {
      gl.bindRenderbuffer(RENDERBUFFER, renderbuffer.handle);
      return Renderbuffer;
    }

    static unbind() {
      gl.bindRenderbuffer(RENDERBUFFER, null);
      return Renderbuffer;
    }

    static storage(format, width, height) {
      gl.renderbufferStorage(RENDERBUFFER, format, width, height);
      return Renderbuffer;
    }

    storageMultisample(samples, format, width, height) {
      gl.renderbufferStorageMultisample(RENDERBUFFER, samples, format, width, height);
      return this;
    }
  }

  return Renderbuffer;
};
