    // fixed X, non-fixed Y
    rotate() {
      const a = this.speed.arr;

      this.obj
        .postRotateX(a[0])
        .preRotateY(a[1]);

      return this;
    }

    // non-fixed X, non-fixed Y
    // rotate() {
