    update() {
      // this.obj.update();

      this.obj.matrix
        .makeRotationQuat(this.obj.quaternion)
        .preTranslate(this.obj.origin, 1)
        .postTranslate(this.obj.translation, 1);

      this.camera.viewMatrix
        .copy(this.obj.matrix)
        .inverseTransformNoScale();

      // this.camera.projection_view_matrix
      //   .copy(this.camera.viewMatrix)
      //   .premultiply(this.camera.projectionMatrix);

      this.camera.projection_view_matrix
        .copy(this.camera.projectionMatrix)
        .postmultiply(this.camera.viewMatrix);

      return this;
    }

