/*
eslint-disable
linebreak-style,
no-magic-numbers,
prefer-destructuring,
id-length,
id-match,
no-bitwise,
array-element-newline,
max-params,
max-len,
*/

export default function ({
	webgl_rendering_context,
	DataArrayConstructor,
	Program,
	$,
	// MAX_PROGRAM_UNIFORM_COUNT,
})
{
	const gl = webgl_rendering_context;

	const uniformArraySizeDivider = {
		'int[]': 1,

		'float[]': 1,

		'ivec2[]': 2,

		'vec2[]': 2,
	};

	const uniformCommands = {
		ivec2: (location, arr) => gl.uniform2i(location, arr[0], arr[1]),

		vec2: (location, arr) => gl.uniform2f(location, arr[0], arr[1]),

		'int[]': (location, arr) => gl.uniform1iv(location, arr),

		'float[]': (location, arr) => gl.uniform1fv(location, arr),

		'ivec2[]': (location, arr) => gl.uniform2iv(location, arr),

		'vec2[]': (location, arr) => gl.uniform2fv(location, arr),

		'int[i]': (i) => (location, arr) => gl.uniform1i(location, arr[i]),

		'float[i]': (i) => (location, arr) => gl.uniform1f(location, arr[i]),

		'ivec2[i]': (i) => (location, arr) => gl.uniform2i(location, arr[i], arr[i + 1]),

		'vec2[i]': (i) => (location, arr) => gl.uniform2f(location, arr[i], arr[i + 1]),
	};

	class Vec2_ {
		constructor() {
			this.arr = new DataArrayConstructor([ 0, 0 ]);
		}

		set(x, y) {
			const a = this.arr;

			a[0] = x;
			a[1] = y;

			return this;
		}

		copy(vec) {
			const va = vec.arr;

			return this.set(va[0], va[1]);
		}

		mulS(s) {
			const a = this.arr;

			a[0] *= s;
			a[1] *= s;

			return this;
		}

		mulS2(x, y) {
			const a = this.arr;

			a[0] *= x;
			a[1] *= y;

			return this;
		}

		makeBezierCurve3Point(p0, p1, p2, p3, t) {
			const v = this.vec2;
			const a = 1 - t;
			const b = a * a;
			const c = t * t;
			const d = a * b;
			const e = 3 * t * b;
			const f = 3 * c * a;
			const g = c * t;

			return this
				.reset()
				.add(v.copy(p0).mulS(d))
				.add(v.copy(p1).mulS(e))
				.add(v.copy(p2).mulS(f))
				.add(v.copy(p3).mulS(g));
		}
	}

	class Vec2 {
		constructor(arr) {
			this.class = 'Vec2';

			this.arr = arr || new DataArrayConstructor(2);

			this.containers = [];

			this.within_container_indices = [];

			this.container_count = 0;

			this.updated_container_count = 0;

			this.vec2 = new Vec2_();
		}

		x(x) {
			this.arr[0] = x;

			return this;
		}

		y(y) {
			this.arr[1] = y;

			return this;
		}

		set(x, y) {
			const a = this.arr;

			a[0] = x;
			a[1] = y;

			return this;
		}

		reset() {
			return this.set(0, 0);
		}

		copy(vec) {
			const va = vec.arr;

			return this.set(va[0], va[1]);
		}

		clone() {
			return new Vec2().copy(this);
		}

		negate() {
			const a = this.arr;

			a[0] *= -1;
			a[1] *= -1;

			return this;
		}

		add(vec) {
			const a = this.arr;
			const va = vec.arr;

			a[0] += va[0];
			a[1] += va[1];

			return this;
		}

		addX(value) {
			this.arr[0] += value;

			return this;
		}

		addY(value) {
			this.arr[1] += value;

			return this;
		}

		addS(s) {
			const a = this.arr;

			a[0] += s;
			a[1] += s;

			return this;
		}

		addS2(x, y) {
			const a = this.arr;

			a[0] += x;
			a[1] += y;

			return this;
		}

		sub(vec) {
			const a = this.arr;
			const va = vec.arr;

			a[0] -= va[0];
			a[1] -= va[1];

			return this;
		}

		subX(value) {
			this.arr[0] -= value;

			return this;
		}

		subY(value) {
			this.arr[1] -= value;

			return this;
		}

		subS(s) {
			const a = this.arr;

			a[0] -= s;
			a[1] -= s;

			return this;
		}

		subS2(x, y) {
			const a = this.arr;

			a[0] -= x;
			a[1] -= y;

			return this;
		}

		mul(vec) {
			const a = this.arr;
			const va = vec.arr;

			a[0] *= va[0];
			a[1] *= va[1];

			return this;
		}

		mulX(value) {
			this.arr[0] *= value;

			return this;
		}

		mulY(value) {
			this.arr[1] *= value;

			return this;
		}

		mulS(s) {
			const a = this.arr;

			a[0] *= s;
			a[1] *= s;

			return this;
		}

		mulS2(x, y) {
			const a = this.arr;

			a[0] *= x;
			a[1] *= y;

			return this;
		}

		div(vec) {
			const a = this.arr;
			const va = vec.arr;

			a[0] /= va[0];
			a[1] /= va[1];

			return this;
		}

		divX(value) {
			this.arr[0] /= value;

			return this;
		}

		divY(value) {
			this.arr[1] /= value;

			return this;
		}

		divS(s) {
			const a = this.arr;

			a[0] /= s;
			a[1] /= s;

			return this;
		}

		divS2(x, y) {
			const a = this.arr;

			a[0] /= x;
			a[1] /= y;

			return this;
		}

		lengthSquare() {
			const a = this.arr;

			return (a[0] * a[0]) + (a[1] * a[1]);
		}

		dot(vec) {
			const a = this.arr;
			const va = vec.arr;

			return (a[0] * va[0]) + (a[1] * va[1]);
		}

		length() {
			return Math.sqrt(this.lengthSquare());
		}

		normalize() {
			return this.divS(this.length() || 1);
		}

		angle(vec) {
			return Math.acos(this.dot(vec) / (Math.sqrt(this.lengthSquare() * vec.lengthSquare()))) || 0;
		}

		projectOnVector(vec) {
			const d = this.dot(vec);
			const c = d / vec.lengthSquare();

			return this
				.copy(vec)
				.mulS(c);
		}

		translate(axis, distance) {
			const aa = axis.arr;
			const x = aa[0];
			const y = aa[1];

			return this
				.addX(x * distance)
				.addY(y * distance);
		}

		makeBezierCurve2Point(p0, p1, p2, t) {
			const v = this.vec2;
			const a = 1 - t;
			const b = a * a;
			const c = 2 * t * a;
			const d = t * t;

			return this
				.reset()
				.add(v.copy(p0).mulS(b))
				.add(v.copy(p1).mulS(c))
				.add(v.copy(p2).mulS(d));
		}

		makeBezierCurve3Point(p0, p1, p2, p3, t) {
			const v = this.vec2;
			const a = 1 - t;
			const b = a * a;
			const c = t * t;
			const d = a * b;
			const e = 3 * t * b;
			const f = 3 * c * a;
			const g = c * t;

			return this
				.reset()
				.add(v.copy(p0).mulS(d))
				.add(v.copy(p1).mulS(e))
				.add(v.copy(p2).mulS(f))
				.add(v.copy(p3).mulS(g));
		}

		static makeBezierCurve3Sequence(p0, p1, p2, p3, segmentCount, p_) {
			const arr = new Float64Array(segmentCount);
			const p = p_ || new Vec2();
			let last = 0;

			for (let i = 0; i < segmentCount; i++) {
				p.makeBezierCurve3Point(p0, p1, p2, p3, i / segmentCount);
				const curr = ~(-p.arr[0] * segmentCount) + 1;
				arr[curr] = p.arr[1];
				arr.set(new Array(curr - last).fill(arr[curr]), last + 1);
				last = curr;
			}

			arr.set(new Array(segmentCount - last - 1).fill(1), last + 1);

			return arr;
		}

		static getIntepolationFunction(sequence) {
			const len = sequence.length;

			return (t) => sequence[~(-t * len) + 1];
		}

		limitX(min, max) {
			this.arr[0] = Math.max(Math.min(this.arr[0], max), min);
		}

		limitY(min, max) {
			this.arr[1] = Math.max(Math.min(this.arr[1], max), min);
		}

		get value() {
			return this.arr[0];
		}

		to(v) {
			return this
				.sub(v)
				.negate();
		}

		from(v) {
			return this.sub(v);
		}


		equals(m) {
			return (

				this.arr[0] === m.arr[0] &&
				this.arr[1] === m.arr[1]
			);
		}

		inject(name, type = 'vec2', location_ = 'auto') {
			const program = Program.currently_being_created;
			// const index_in_container = program.uniformCount++;
			const index_in_container = program.uniform_stack.arr.length;
			let line = `uniform ${ type.replace(/\[\]/, '').replace(/\[[0-9]+\]/g, '') } ${ name }`;
			let location = null;

			program.uniforms[name] = this;

			program.uniform_stack.extend();
			this.containers.push(program);
			this.within_container_indices[this.container_count++] = index_in_container;

			if (location_ === 'auto') {
				program.afterLinkingActions.push(() => {
					location = program.getUniformLocation(name);

					// const command = uniformCommands[type];
					const index = type.match(/\[[0-9]+\]/g);
					const command = index ? uniformCommands[`${ type.replace(/\[[0-9]+\]/g, '') }[i]`](parseInt(index[0].replace(/\[|\]/g, ''), 10)) : uniformCommands[type];

					const updateFunction = () => {
						command(location, this.arr);
						this.updated_container_count = 0;
					};

					program.uniformUpdateFunctionsArray.push(updateFunction);
					program.uniformUpdateFunctionsObject[name] = updateFunction;

					updateFunction();
				});
			} else {
				location = location_;
				line = `layout (location = ${ location }) ${ line }`;
			}

			if (type.match(/\[\]/)) {
				line += `[${ this.arr.length / uniformArraySizeDivider[type] }]`;
			}

			return `${ line };`;
		}

		$() {
			// const len = this.container_count - this.updated_container_count;
			// this.updated_container_count = this.container_count;

			// for (let i = 0; i < len; i++) {
			//   const program = this.containers[i];
			//   program.uniform_stack.push(this.within_container_indices[i]);
			// }

			if (this.updated_container_count === 0) {
				for (; this.updated_container_count < this.container_count; ++this.updated_container_count) {
					const program = this.containers[this.updated_container_count];
					// program.uniform_stack.push(this.within_container_indices[this.updated_container_count]);
					program.uniform_stack.push(program.uniformUpdateFunctionsArray[this.within_container_indices[this.updated_container_count]]);
				}

				$();
			}

			return this;
		}
	}

	return Vec2;
};
