/*
eslint-disable
linebreak-style,
max-params,
no-magic-numbers,
func-style,
max-len
*/

const getShaderAttribQualifier = (contextType) => ({
  webgl: 'attribute',
  webgl2: 'in',
})[contextType];

export default function ({
  webgl_rendering_context,
  contextType,
  Program,
})
{
  const gl = webgl_rendering_context;

  const {
    FLOAT,
    ARRAY_BUFFER,
  } = gl;

  class VertexAttrib {
    static enable(location = 0) {
      if (location.length) {
        location.forEach((elm) => gl.enableVertexAttribArray(elm));
      } else {
        gl.enableVertexAttribArray(location);
      }

      return VertexAttrib;
    }

    static disable(location = 0) {
      if (location.length) {
        location.forEach((elm) => gl.disableVertexAttribArray(elm));
      } else {
        gl.disableVertexAttribArray(location);
      }

      return VertexAttrib;
    }

    static inject(name, location = 'auto', type = 'vec3') {
      const program = Program.currently_being_created;

      let line = `${ getShaderAttribQualifier(contextType) } ${ type } ${ name }`;

      if (location === 'auto') {
        program.afterLinkingActions.push(() => program.getAttribLocation(name));
      } else if (contextType === 'webgl') {
        program.beforeLinkingActions.push(() => program.bindAttribLocation(location, name));
      } else {
        line = `layout (location = ${ location }) ${ line }`;
      }

      return `${ line };`;
    }

    static pointer(location = 0, size = 3, type = FLOAT, normalized = 0, stride = 0, offset = 0) {
      gl.vertexAttribPointer(location, size, type, normalized, stride, offset);

      return VertexAttrib;
    }

    static pointer2(buffer, location = 0, size = 3, type = FLOAT, normalized = 0, stride = 0, offset = 0) {
      gl.bindBuffer(ARRAY_BUFFER, buffer.handle);
      gl.vertexAttribPointer(location, size, type, normalized, stride, offset);

      return VertexAttrib;
    }
  }

  return VertexAttrib;
};
