export default function (

  {
    webgl_rendering_context,
  }
)
{
  const gl = webgl_rendering_context;

  class Buffer
  {
    constructor ()
    {
      this.handle = gl.createBuffer();
    }

    bind (target)
    {
      gl.bindBuffer(target, this.handle);

      return this;
    }

    bindBase (target)
    {
      gl.bindBufferBase(target, this.handle);

      return this;
    }

    bindRange (target)
    {
      gl.bindBufferRange(target, this.handle);

      return this;
    }

    unbind (target)
    {
      gl.bindBuffer(target, null);

      return this;
    }

    data (target, srcData, usage = gl.STATIC_DRAW)
    {
      gl.bufferData(target, srcData, usage);

      return this;
    }

    subData (target, offset, srcData)
    {
      gl.bufferSubData(target, offset, srcData);

      return this;
    }

    delete ()
    {
      gl.deleteBuffer(this.handle);

      return this;
    }

    static bind (target, buffer)
    {
      gl.bindBuffer(target, buffer.handle);

      return Buffer;
    }

    // static bindBase (target, buffer)
    // {
    //   gl.bindBufferBase(target, buffer.handle);

    //   return Buffer;
    // }

    // static bindRange (target, buffer)
    // {
    //   gl.bindBufferRange(target, buffer.handle);

    //   return Buffer;
    // }

    static unbind (target)
    {
      gl.bindBuffer(target, null);

      return Buffer;
    }

    static data (target, srcData, usage = gl.STATIC_DRAW)
    {
      gl.bufferData(target, srcData, usage);

      return Buffer;
    }

    static subData (target, offset, srcData)
    {
      gl.bufferSubData(target, offset, srcData);

      return Buffer;
    }

    static delete (buffer)
    {
      gl.deleteBuffer(buffer.handle);

      return Buffer;
    }
  }

  return Buffer;
};
