export default function ({
  webgl_rendering_context,
  DataArrayConstructor,
  Program,
  Vec4,
  $,
})
{
  const gl = webgl_rendering_context;

  const uniformCommands = {
    ivec4: (location, arr) => gl.uniform4i(location, arr[0], arr[1], arr[2], arr[3]),

    vec4: (location, arr) => gl.uniform4f(location, arr[0], arr[1], arr[2], arr[3]),

    'ivec4[]': (location, arr) => gl.uniform4iv(location, arr),

    'vec4[]': (location, arr) => gl.uniform4fv(location, arr),
  };

  class AuxillaryQuat {
    constructor() {
      this.arr = new DataArrayConstructor([ 0, 0, 0, 1 ]);
    }

    set(x, y, z, w) {
      const a = this.arr;

      a[0] = x;
      a[1] = y;
      a[2] = z;
      a[3] = w;

      return this;
    }

    copy(quaternion) {
      const qa = quaternion.arr;

      return this.set(qa[0], qa[1], qa[2], qa[3]);
    }

    add(vec4) {
      const a = this.arr;
      const va = vec4.arr;

      a[0] += va[0];
      a[1] += va[1];
      a[2] += va[2];
      a[3] += va[3];

      return this;
    }

    mulS(s) {
      const a = this.arr;

      a[0] *= s;
      a[1] *= s;
      a[2] *= s;
      a[3] *= s;

      return this;
    }

    mulS2(x, y, z, w) {
      const a = this.arr;

      a[0] *= x;
      a[1] *= y;
      a[2] *= z;
      a[3] *= w;

      return this;
    }

    makeRotation(axis, angle) {
      const aa = axis.arr;
      const ang = angle * 0.5;
      const sin = Math.sin(ang);

      return this.set(aa[0] * sin, aa[1] * sin, aa[2] * sin, Math.cos(ang));
    }

    makeRotationX(angle) {
      const ang = angle * 0.5;

      return this.set(Math.sin(ang), 0, 0, Math.cos(ang));
    }

    makeRotationY(angle) {
      const ang = angle * 0.5;

      return this.set(0, Math.sin(ang), 0, Math.cos(ang));
    }

    makeRotationZ(angle) {
      const ang = angle * 0.5;

      return this.set(0, 0, Math.sin(ang), Math.cos(ang));
    }
  }

  class Quat extends Vec4 {
    constructor(arr) {
      super(arr);

      this.class = 'Quat';

      this.aux_quat1 = new AuxillaryQuat();
      this.aux_quat2 = new AuxillaryQuat();
    }

    clone() {
      return new Quat().copy(this);
    }

    multiply(a, b) {
      const A = a.arr;
      const B = b.arr;

      return this.set(
        (A[0] * B[3]) + (A[3] * B[0]) + (A[1] * B[2]) - (A[2] * B[1]),
        (A[1] * B[3]) + (A[3] * B[1]) + (A[2] * B[0]) - (A[0] * B[2]),
        (A[2] * B[3]) + (A[3] * B[2]) + (A[0] * B[1]) - (A[1] * B[0]),
        (A[3] * B[3]) - (A[0] * B[0]) - (A[1] * B[1]) - (A[2] * B[2])
      );
    }

    premultiply(quaternion) {
      return this.multiply(quaternion, this);
    }

    postmultiply(quaternion) {
      return this.multiply(this, quaternion);
    }

    // simple conjugate for unit quaternion
    conjugate() {
      const a = this.arr;

      a[0] = -a[0];
      a[1] = -a[1];
      a[2] = -a[2];

      return this;
    }

    makeRotation(axis, angle) {
      const aa = axis.arr;
      const ang = angle * 0.5;
      const sin = Math.sin(ang);

      return this.set(aa[0] * sin, aa[1] * sin, aa[2] * sin, Math.cos(ang));
    }

    preRotate(axis, angle) {
      return this.premultiply(this.aux_quat1.makeRotation(axis, angle));
    }

    postRotate(axis, angle) {
      return this.postmultiply(this.aux_quat1.makeRotation(axis, angle));
    }

    makeRotationX(angle) {
      const ang = angle * 0.5;

      return this.set(Math.sin(ang), 0, 0, Math.cos(ang));
    }

    preRotateX(angle) {
      return this.premultiply(this.aux_quat1.makeRotationX(angle));
    }

    postRotateX(angle) {
      return this.postmultiply(this.aux_quat1.makeRotationX(angle));
    }

    makeRotationY(angle) {
      const ang = angle * 0.5;

      return this.set(0, Math.sin(ang), 0, Math.cos(ang));
    }

    preRotateY(angle) {
      return this.premultiply(this.aux_quat1.makeRotationY(angle));
    }

    postRotateY(angle) {
      return this.postmultiply(this.aux_quat1.makeRotationY(angle));
    }

    makeRotationZ(angle) {
      const ang = angle * 0.5;

      return this.set(0, 0, Math.sin(ang), Math.cos(ang));
    }

    preRotateZ(angle) {
      return this.premultiply(this.aux_quat1.makeRotationZ(angle));
    }

    postRotateZ(angle) {
      return this.postmultiply(this.aux_quat1.makeRotationZ(angle));
    }

    // q0 and q1 must be normalized
    slerp(q0, q1, t) {
      const dot = q0.dot(q1);
      const theta0 = Math.acos(dot);
      const theta = theta0 * t;
      const sinTheta = Math.sin(theta);
      const sinTheta0 = Math.sin(theta0);
      const s0 = Math.cos(theta) - (dot * sinTheta / sinTheta0);
      const s1 = sinTheta / sinTheta0;

      this.aux_quat1
        .copy(q1)
        .mulS(s1);

      this.aux_quat2
        .copy(q0)
        .mulS(s0)
        .add(this.aux_quat1);

      return this.copy(this.aux_quat2);
    }

    // // validate
    // extractFromMatrix(m, p) {
    //   const ma = m.arr;
    //   // const w = Math.sqrt(1 + ma[0] + ma[5] + ma[10]) * 0.5 || 1;
    //   // const w4 = w * 4;

    //   // return this.set(
    //   //   (ma[9] - ma[6]) / w4,
    //   //   (ma[2] - ma[8]) / w4,
    //   //   (ma[4] - ma[1]) / w4,
    //   //   w
    //   // );

    //   const trace = ma[0] + ma[5] + ma[10];

    //   if (trace > 0) {
    //     const k = 0.5 / Math.sqrt(1 + trace);

    //     this.set(k * (ma[9] - ma[6]), k * (ma[2] - ma[8]), k * (ma[4] - ma[1]), 0.25 / k);
    //   } else if ((ma[0] > ma[5]) && (ma[0] > ma[10])) {
    //     const k = 0.5 / Math.sqrt(1 + ma[0] - ma[5] - ma[10]);

    //     this.set(0.25 / k, k * (ma[4] + ma[1]), k * (ma[8] + ma[2]), k * (ma[9] - ma[6]));
    //   } else if (ma[5] > ma[10]) {
    //     const k = 0.5 / Math.sqrt(1 + ma[5] - ma[0] - ma[10]);

    //     this.set(k * (ma[4] + ma[1]), 0.25 / k, k * (ma[9] + ma[6]), k * (ma[2] - ma[8]));
    //   } else {
    //     const k = 0.5 / Math.sqrt(1 + ma[10] - ma[0] - ma[5]);

    //     this.set(k * (ma[8] + ma[2]), k * (ma[9] + ma[6]), 0.25 / k, k * (ma[4] - ma[1]));
    //   }

    //   return this;
    // }

    // VALIDATE !!!
    // extractFromMatrix(m) {
    //   const ma = m.arr;
    //   let t = 0;

    //   if (ma[10] < 0) {
    //     if (ma[0] > ma[5]) {
    //       t = 1 + ma[0] - ma[5] - ma[10];

    //       this.set(t, ma[4] + ma[1], ma[2] + ma[8], ma[9] - ma[6]);
    //     } else {
    //       t = 1 - ma[0] + ma[5] - ma[10];

    //       this.set(ma[4] + ma[1], t, ma[9] + ma[6], ma[2] - ma[8]);
    //     }
    //   } else if (ma[0] < -ma[5]) {
    //     t = 1 - ma[0] - ma[5] + ma[10];

    //     this.set(ma[2] + ma[8], ma[9] + ma[6], t, ma[4] - ma[1]);
    //   } else {
    //     t = 1 + ma[0] + ma[5] + ma[10];

    //     this.set(ma[9] - ma[6], ma[2] - ma[8], ma[4] - ma[1], t);
    //   }

    //   return this.mulS(0.5 / Math.sqrt(t));
    // }

    // refactor
    extractFromMatrix(m) {
      // http://www.euclideanspace.com/maths/geometry/rotations/conversions/matrixToQuaternion/index.htm

      // assumes the upper 3x3 of m is a pure rotation matrix (i.e, unscaled)

      const te = m.arr;

      const m11 = te[0];
      const m12 = te[4];
      const m13 = te[8];
      const m21 = te[1];
      const m22 = te[5];
      const m23 = te[9];
      const m31 = te[2];
      const m32 = te[6];
      const m33 = te[10];

      const trace = m11 + m22 + m33;

      let s = 0;

      if (trace > 0) {
        s = 0.5 / Math.sqrt(trace + 1.0);

        this.arr[3] = 0.25 / s;
        this.arr[0] = (m32 - m23) * s;
        this.arr[1] = (m13 - m31) * s;
        this.arr[2] = (m21 - m12) * s;
      } else if (m11 > m22 && m11 > m33) {
        s = 2.0 * Math.sqrt(1.0 + m11 - m22 - m33);

        this.arr[3] = (m32 - m23) / s;
        this.arr[0] = 0.25 * s;
        this.arr[1] = (m12 + m21) / s;
        this.arr[2] = (m13 + m31) / s;
      } else if (m22 > m33) {
        s = 2.0 * Math.sqrt(1.0 + m22 - m11 - m33);

        this.arr[3] = (m13 - m31) / s;
        this.arr[0] = (m12 + m21) / s;
        this.arr[1] = 0.25 * s;
        this.arr[2] = (m23 + m32) / s;
      } else {
        s = 2.0 * Math.sqrt(1.0 + m33 - m11 - m22);

        this.arr[3] = (m21 - m12) / s;
        this.arr[0] = (m13 + m31) / s;
        this.arr[1] = (m23 + m32) / s;
        this.arr[2] = 0.25 * s;
      }

      return this;
    }

    makeFromVec3(v0, v1) {
      const EPS = 0.000001;

      let r = v0.dot(v1) + 1;

      if (r < EPS) {
        r = 0.0;

        if (Math.abs(v0.arr[0]) > Math.abs(v0.arr[2])) {
          this.arr[0] = -v0.arr[1];
          this.arr[1] = v0.arr[0];
          this.arr[2] = 0;
          this.arr[3] = r;
        } else {
          this.arr[0] = 0;
          this.arr[1] = -v0.arr[2];
          this.arr[2] = v0.arr[1];
          this.arr[3] = r;
        }
      } else {
        this.arr[0] = (v0.arr[1] * v1.arr[2]) - (v0.arr[2] * v1.arr[1]);
        this.arr[1] = (v0.arr[2] * v1.arr[0]) - (v0.arr[0] * v1.arr[2]);
        this.arr[2] = (v0.arr[0] * v1.arr[1]) - (v0.arr[1] * v1.arr[0]);
        this.arr[3] = r;
      }

      return this.normalize();
    }

    equals(m) {
      return (

        this.arr[0] === m.arr[0] &&
        this.arr[1] === m.arr[1] &&
        this.arr[2] === m.arr[2] &&
        this.arr[3] === m.arr[3]
      );
    }

    angle(q) {
      return (Math.acos(this.dot(q)));
    }

    inject(name, type = 'vec4', location_ = 'auto') {
      const program = Program.currently_being_created;
      // const index_in_container = program.uniformCount++;
      const index_in_container = program.uniform_stack.arr.length;
      let line = `uniform ${ type.replace(/\[\]/, '') } ${ name }`;
      let location = null;

      program.uniforms[name] = this;

      program.uniform_stack.extend();
      this.containers.push(program);
      this.within_container_indices[this.container_count++] = index_in_container;

      if (location_ === 'auto') {
        program.afterLinkingActions.push(() => {
          location = program.getUniformLocation(name);

          const command = uniformCommands[type];

          const updateFunction = () => {
            command(location, this.arr);
            this.updated_container_count = 0;
          };

          program.uniformUpdateFunctionsArray.push(updateFunction);
          program.uniformUpdateFunctionsObject[name] = updateFunction;

          updateFunction();
        });
      } else {
        location = location_;
        line = `layout (location = ${ location }) ${ line }`;
      }

      if (type.match(/\[\]/)) {
        line += `[${ this.arr.length }]`;
      }

      return `${ line };`;
    }

    $() {
      // const len = this.container_count - this.updated_container_count;
      // this.updated_container_count = this.container_count;

      // for (let i = 0; i < len; i++) {
      //   const program = this.containers[i];
      //   program.uniform_stack.push(this.within_container_indices[i]);
      // }

      if (this.updated_container_count === 0) {
        for (; this.updated_container_count < this.container_count; ++this.updated_container_count) {
          const program = this.containers[this.updated_container_count];
          // program.uniform_stack.push(this.within_container_indices[this.updated_container_count]);
          program.uniform_stack.push(program.uniformUpdateFunctionsArray[this.within_container_indices[this.updated_container_count]]);
        }

        $();
      }

      return this;
    }
  }

  return Quat;
};
