// stack is array of ids (typed array is preferrable for best performance)
// size of stack is max number of transitions updated in the same time
// type of stack array describes max transition id value, f. e. max transition id value for Uint8Array is 255

const IDLE_FUNCTION = () => 0;

export default function ()
{
  class Transition {
    constructor(timeObject) {
      this.class = 'Transition';
      this.index = Transition.arr.push(this) - 1;
      this.active = 0;
      this.stackIndex = 0;
      this.time = 0;
      this.interpolation = 0;
      this.duration = 0;
      this.onProgress = null;
      this.onEnd = null;
      this.timeObject = timeObject;
      this.stack = timeObject.stack;
      this.frameTime = timeObject.frameTime.arr;
    }

    set(duration = 0, onProgress = IDLE_FUNCTION, onEnd = IDLE_FUNCTION) {
      this.cancel();
      this.duration = duration;
      this.onProgress = onProgress;
      this.onEnd = onEnd;
      this.time = 0;
      this.active = 1;
      this.stackIndex = this.timeObject.activeTransitionCount;
      this.stack[this.timeObject.activeTransitionCount++] = this.index + 1;
      return this;
    }

    set2(duration = 0, startTime, onProgress = IDLE_FUNCTION, onEnd = IDLE_FUNCTION) {
      this.cancel();
      this.duration = duration;
      this.onProgress = onProgress;
      this.onEnd = onEnd;
      this.time = startTime;
      this.active = 1;
      this.stackIndex = this.timeObject.activeTransitionCount;
      this.stack[this.timeObject.activeTransitionCount++] = this.index + 1;
      return this;
    }

    update() {
      this.interpolation = this.time / this.duration;
      const floorInterp = ~~this.interpolation;
      // Transition.updateTransitionSwitсh[(floorInterp / floorInterp) | 0](this);

      if (floorInterp)
      {
        this
          .cancel()
          .onEnd(this);
      }
      else
      {
        this.time += this.frameTime[0];
        this.onProgress(this);
      }

      return this;
    }

    cancel() {
      this.interpolation = 0;
      // this.timeObject.spliceTransitionSwitch[this.active](this);

      if (this.active)
      {
        this.timeObject.spliceStack(this)
      }

      return this;
    }
  }

  Transition.arr = [];

  // Transition.updateTransitionSwitсh = [
  //   (obj) => {
  //     obj.time += obj.frameTime[0];
  //     obj.onProgress(obj);
  //   },

  //   (obj) => obj
  //     .cancel()
  //     .onEnd(obj),
  // ];

  return Transition;
};
