export default function (

	{
		webgl_rendering_context,
		Buffer,
	},
)
{
	const gl = webgl_rendering_context;

	class IndexBuffer extends Buffer
	{
		bind ()
		{
			gl.bindBuffer(gl.ELEMENT_ARRAY_BUFFER, this.handle);

			return this;
		}

		data (srcData, usage = gl.STATIC_DRAW)
		{
			gl.bufferData(gl.ELEMENT_ARRAY_BUFFER, srcData, usage);

			return this;
		}

		subData (offset, srcData)
		{
			gl.bufferSubData(gl.ELEMENT_ARRAY_BUFFER, offset, srcData);

			return this;
		}

		static bind (indexBuffer)
		{
			gl.bindBuffer(gl.ELEMENT_ARRAY_BUFFER, indexBuffer.handle);

			return IndexBuffer;
		}

		static unbind ()
		{
			gl.bindBuffer(gl.ELEMENT_ARRAY_BUFFER, null);

			return IndexBuffer;
		}

		static data (srcData, usage = gl.STATIC_DRAW)
		{
			gl.bufferData(gl.ELEMENT_ARRAY_BUFFER, srcData, usage);

			return IndexBuffer;
		}

		static subData (offset, srcData)
		{
			gl.bufferSubData(gl.ELEMENT_ARRAY_BUFFER, offset, srcData);

			return IndexBuffer;
		}
	}

	return IndexBuffer;
}
