/*
eslint-disable
linebreak-style,
id-match,
id-length,
func-style,
no-magic-numbers,
no-bitwise,
max-len
*/

const IDLE_FUNCTION = () => 0;

export default function ({
  stateFlag,
  Vec1,
  Transition,
})
{
  const transitionArr = Transition.arr;

  class Time {
    constructor(stack = new Uint8Array(8)) {
      this.class = 'Time';
      this.frameTime = new Vec1();
      this.globalTime = new Vec1();
      this.lastTime = new Vec1().set(Date.now());
      this.animationFrame = null;
      this.stack = stack;
      this.activeTransitionCount = 0;
      this.updateCustoms = null;
      // this.spliceTransitionSwitch = [ IDLE_FUNCTION, (transition) => this.spliceStack(transition) ];
    }

    update() {
      const now = Date.now();
      const fa = this.frameTime.arr;
      const la = this.lastTime.arr;
      fa[0] = now - la[0];
      la[0] = now;
      this.globalTime.arr[0] += fa[0];
      return this;
    }

    updateTransitions() {
      for (let i = 0; i < this.activeTransitionCount; ++i) {
        transitionArr[this.stack[i] - 1].update();
      }
      return this;
    }

    spliceStack(transition) {
      transition.active = 0;
      transitionArr[this.stack[--this.activeTransitionCount] - 1].stackIndex = transition.stackIndex;
      this.stack[transition.stackIndex] = this.stack[this.activeTransitionCount];
      this.stack[this.activeTransitionCount] = 0;
      return this;
    }

    loop(clbk) {
      const loop = () => {
        clbk();
        this.animationFrame = requestAnimationFrame(loop);
      };

      loop();

      return this;
    }

    loop2({
      onStart = IDLE_FUNCTION,
      onStateChange = IDLE_FUNCTION,
      onEnd = IDLE_FUNCTION,
    }) {
      // const renderSwitch = [
      //   IDLE_FUNCTION,

      //   () => {
      //     onStateChange();
      //     stateFlag[0] = 0;
      //   },
      // ];

      const loop = () => {
        this
          .update()
          .updateTransitions();

        onStart();

        // renderSwitch[stateFlag[0]]();

        if (stateFlag[0])
        {
          onStateChange();

          stateFlag[0] = 0;
        }

        onEnd();

        this.animationFrame = requestAnimationFrame(loop);
      };

      onStateChange();

      loop();

      return this;
    }

    loop5(onStateChange) {
      // const renderSwitch = [
      //   IDLE_FUNCTION,

      //   () => {
      //     onStateChange();
      //     stateFlag[0] = 0;
      //   },
      // ];

      // LOG(onStateChange)

      const loop = () => {
        this
          .update()
          .updateTransitions();

        // renderSwitch[stateFlag[0]]();

        if (stateFlag[0])
        {
          onStateChange();

          stateFlag[0] = 0;
        }

        this.animationFrame = requestAnimationFrame(loop);
      };

      loop();

      return this;
    }

    loop4(onStateChange, custom_update_function) {
      const loop = () => {
        this
          .update()
          .updateTransitions();

        custom_update_function();

        // // custom_update_function example
        //
        // if (orbit.needsUpdate)
        // {
        //   orbit.update().$();
        // }

        if (stateFlag[0])
        {
          onStateChange();

          stateFlag[0] = 0;
        }

        this.animationFrame = requestAnimationFrame(loop);
      };

      loop();

      return this;
    }

    loop3(onStateChange) {
      const loop = () => {
        this
          .update()
          .updateTransitions()

        // remove condition ?
        if (this.updateCustoms)
        {
          this.updateCustoms();
        }

        if (stateFlag[0])
        {
          onStateChange();

          stateFlag[0] = 0;
        }

        this.animationFrame = requestAnimationFrame(loop);
      };

      loop();

      return this;
    }

    loop6(onStateChange) {
      const loop = () => {
        this
          .update()
          .updateTransitions()

        // remove condition ?
        if (this.updateCustoms)
        {
          this.updateCustoms();
        }

        onStateChange();

        this.animationFrame = requestAnimationFrame(loop);
      };

      loop();

      return this;
    }

    endLoop() {
      cancelAnimationFrame(this.animationFrame);

      return this;
    }
  }

  return Time;
};
