/*
eslint-disable
linebreak-style,
max-len,
prefer-destructuring,
id-length,
id-match,
array-element-newline,
no-magic-numbers
*/

export default function ({
	webgl_rendering_context,
	DataArrayConstructor,
	Program,
	$,
	// MAX_PROGRAM_UNIFORM_COUNT,
})
{
	const gl = webgl_rendering_context;

	const uniformCommands = {
		int: (location, arr) => gl.uniform1i(location, arr[0]),

		sampler2D: (location, arr) => gl.uniform1i(location, arr[0]),

		float: (location, arr) => gl.uniform1f(location, arr[0]),

		'int[]': (location, arr) => gl.uniform1iv(location, arr),

		'sampler2D[]': (location, arr) => gl.uniform1iv(location, arr),

		'float[]': (location, arr) => gl.uniform1fv(location, arr),
	};

	class Vec1 {
		constructor(arr) {
			this.class = 'Vec1';

			this.arr = arr || new DataArrayConstructor(1);

			this.containers = [];

			this.within_container_indices = [];

			this.container_count = 0;

			this.updated_container_count = 0;
		}

		x(x) {
			this.arr[0] = x;

			return this;
		}

		set(x) {
			this.arr[0] = x;

			return this;
		}

		reset() {
			this.arr[0] = 0;

			return this;
		}

		copy(vec) {
			this.arr[0] = vec.arr[0];

			return this;
		}

		clone() {
			return new Vec1().copy(this);
		}

		negate() {
			this.arr[0] *= -1;

			return this;
		}

		add(vec) {
			this.arr[0] += vec.arr[0];

			return this;
		}

		addS(x) {
			this.arr[0] += x;

			return this;
		}

		sub(vec) {
			this.arr[0] -= vec.arr[0];

			return this;
		}

		subS(x) {
			this.arr[0] -= x;

			return this;
		}

		mul(vec) {
			this.arr[0] *= vec.arr[0];

			return this;
		}

		mulS(x) {
			this.arr[0] *= x;

			return this;
		}

		div(vec) {
			this.arr[0] /= vec.arr[0];

			return this;
		}

		divS(x) {
			this.arr[0] /= x;

			return this;
		}

		// uniformBuffer

		// vertexAttrib

		trim(min, max) {
			this.arr[0] = Math.max(Math.min(this.arr[0], max), min);

			return this;
		}

		not() {
			this.arr[0] = 1 - this.arr[0];

			return this;
		}

		toggle(a, b) {
			this.arr[0] = (this.arr[0] === a ? b : a);

			return this;
		}

		is(value) {
			return this.arr[0] === value;
		}

		equals(value) {
			return this.arr[0] === value;
		}

		one() {
			this.arr[0] = 1;

			return this;
		}

		zero() {
			this.arr[0] = 0;

			return this;
		}

		true() {
			this.arr[0] = 1;

			return this;
		}

		false() {
			this.arr[0] = 0;

			return this;
		}

		get isTrue() {
			return (this.arr[0] === 1);
		}

		get isFalse() {
			return (this.arr[0] === 0);
		}

		get value() {
			return this.arr[0];
		}

		inject (name, type = 'float', __function = null)
		{
			const program = Program.currently_being_created;
			const index_in_container = program.uniform_stack.arr.length;

			program.uniforms[name] = this;

			program.uniform_stack.extend();

			this.containers.push(program);
			this.within_container_indices[this.container_count++] = index_in_container;

			// const command = uniformCommands[type];
			// let location = null;

			program.afterLinkingActions.push(

				() =>
				{
					const location = program.getUniformLocation(name);

					let _function = null;

					if (__function)
					{
						_function = () =>
						{
							__function(location, this.arr);

							this.updated_container_count = 0;
						};
					}
					else
					{
						const command = uniformCommands[type];

						_function = () =>
						{
							command(location, this.arr);

							this.updated_container_count = 0;
						};
					}

					program.uniformUpdateFunctionsArray.push(_function);
					program.uniformUpdateFunctionsObject[name] = _function;

					_function();
				},
			);



			let line = `uniform ${ type.replace(/\[\]/, '') } ${ name }`;

			if (type.match(/\[\]/))
			{
				line += `[${ this.arr.length }]`;
			}

			return `${ line };`;
		}

		$()
		{
			// (updated_container_count > 0) means all the programs this uniform related to
			// were updated with this uniform in current loop iteration
			if (this.updated_container_count)
			{
				return;
			}

			for (; this.updated_container_count < this.container_count; ++this.updated_container_count)
			{
				const program = this.containers[this.updated_container_count];

				program.uniform_stack.push(program.uniformUpdateFunctionsArray[this.within_container_indices[this.updated_container_count]]);
			}

			$();
		}

		// $2(program)
	}

	return Vec1;
};
