export default function (

	{
		webgl_rendering_context,
		Program,
	},
)
{
	const gl = webgl_rendering_context;



	class Stack
	{
		constructor ()
		{
			this.length = 0;

			// rename to elements ro smth
			this.arr = [];
		}

		extend ()
		{
			this.arr.push(null);
		}

		push (_function)
		{
			this.arr[this.length] = _function;

			++this.length;
		}
	}



	class UniformSet
	{
		static instance_count = 0;

		constructor (data_objects, binding)
		{
			this.index = UniformSet.instance_count++;

			this.uniform_buffer = gl.createBuffer();

			this.uniform_stack = new Stack();

			this.uniforms = {};

			this.uniformUpdateFunctionsArray = [];
			this.uniformUpdateFunctionsObject = {};

			this.data_objects = data_objects;

			this.binding = binding;

			this.size = 0;

			this.after_actions = [];

			this.test =
				Object.keys(this.data_objects)
					.map(

						(name, name_index, name_array) =>
						{
							if (name_index === name_array.length - 1)
							{
								// LOG(this.data_objects[name].offset + this.data_objects[name].target.arr.length * 4)
								this.size = this.data_objects[name].offset + this.data_objects[name].target.arr.length * 4;
								// LOG(this.size)
							}

							return this.data_objects[name].target.injectToUniformSet(this, name, this.data_objects[name].offset);
						},
					)
					.join('\n\t')

			// gl.bindBuffer(gl.UNIFORM_BUFFER, this.uniform_buffer);
			gl.bindBufferBase(gl.UNIFORM_BUFFER, this.binding, this.uniform_buffer);
			// gl.bindBufferRange(gl.UNIFORM_BUFFER, this.binding, this.uniform_buffer, 0, this.size);

			gl.bufferData(gl.UNIFORM_BUFFER, this.size, gl.DYNAMIC_DRAW);

			this.after_actions.forEach((_) => _());

			gl.bindBuffer(gl.UNIFORM_BUFFER, null);

			// Object.keys(this.data_objects).forEach(

			// 	(name) =>
			// 	{
			// 		this.data_objects[name].injectToUniformSet(this, name, 0);
			// 	},
			// );
		}

		use ()
		{
			gl.bindBuffer(gl.UNIFORM_BUFFER, this.uniform_buffer);

			return this;
		}

		use2 ()
		{
			gl.bindBuffer(gl.UNIFORM_BUFFER, this.uniform_buffer);

			return this.updateUniforms();
		}

		use3 ()
		{
			const len = this.uniform_stack.length;

			if (len)
			{
				gl.bindBuffer(gl.UNIFORM_BUFFER, this.uniform_buffer);
				// gl.bindBufferBase(gl.UNIFORM_BUFFER, this.binding, this.uniform_buffer);

				this.uniform_stack.length = 0;

				// This is done to reduce number of conditional tests.
				// If len > 0 then 0 element of stack exists
				// and there is no need to test if 0 < len so
				// first iteration of loop can be avoided.

				/* eslint-disable-next-line prefer-destructuring */
				const _function = this.uniform_stack.arr[0];

				_function();

				for (let i = 1; i < len; ++i)
				{
					const __function = this.uniform_stack.arr[i];

					__function();
				}
			}

			// gl.bindBuffer(gl.UNIFORM_BUFFER, null);

			return this;
		}

		updateUniform (name)
		{
			this.uniformUpdateFunctionsObject[name]();

			return this;
		}

		updateUniforms ()
		{
			const len = this.uniformUpdateFunctionsArray.length;

			for (let i = 0; i < len; ++i)
			{
				const _function = this.uniformUpdateFunctionsArray[i];

				_function();
			}

			return this;
		}

		inject (layout, block_name, instance_name)
		{
			// shader explicit uniform block binding is not supported in glsl 300

			const program = Program.currently_being_created;

			program.afterLinkingActions.push(

				() =>
				{
					gl.uniformBlockBinding(

						program.handle,
						gl.getUniformBlockIndex(program.handle, block_name),
						this.binding,
					);
				},
			);

			return (

`layout (${ layout }) uniform ${ block_name || `UniformBlock${ this.index }` }
{
	${ this.test }
}${ ` ${ instance_name }` || '' };`
			);
		}

		// updateUniformStackUniforms ()
		// {
		// 	const len = this.uniform_stack.length;

		// 	this.uniform_stack.length = 0;

		// 	for (let i = 0; i < len; ++i)
		// 	{
		// 		const _function = this.uniform_stack.arr[i];

		// 		_function();
		// 	}

		// 	return this;
		// }
	}

	return UniformSet;
}
